<template>
    <div class="comment_service">
        <div v-if="is_show">
        <ul>
            <li class="clearfix" v-for="item in orderList" :key="item.id" >
                <div class="headPortrait">
                    <img v-if="item.image" :src='"http://dev.jzfw.yaokexing.com"+item.image' alt="">
                    <img v-else src="../../assets/my/pic/微信支付图标.png" alt="">
                    <span>{{item.full_name}}</span>
                </div>
                <div class="accomplishInfo">
                    <div><span>服务标题：</span>{{item.name}}</div>
                    <div><span>数量：</span>1</div>
                    <div><span>费用合计：</span>{{item.price_real}}</div>
                    <div><span>下单时间：</span>{{item.addtime}}</div>
                    <div style="color:#00c88d"><span>交易状态：</span>已完成</div>
                    <div><span  @click="listItem(item.id)" class="order-detail order">订单</span><span @click="comment_order(item.id)" class="order-detail">评价</span></div>
                </div>
            </li>
        </ul>
        </div>
        <div v-else>
            <div class="black-detail">
                <img src="../../assets/aa.gif">
                <p>咦 ~，没有数据呢 ~</p>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    data(){
        return{
            orderList:[],
            page:1,
            page_count:1,
            end:0,
            is_show:0
        }
    },
    methods:{
        getOrderList(){
            let getParams = window.localStorage.getItem('params');
            if (getParams == null) {
                this.$router.push({
                    path: 'login',
                })
            }else{
                let jsonObj = JSON.parse(getParams);
                let queryData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '1',
                    timestamp: "222",
                    per_page: 1,
                    type: 3,
                }
                this.$http.get(this.$api.orderList, { params: queryData })
                .then(rsp => {
                    this.orderList = rsp.data.result.list;
                    if(rsp.data.result.list.length){
                        this.is_show = 1
                    }else{
                        this.is_show = 0
                    }
                })
            }
        },
        comment_order(id){
            this.$router.push({
                path: '/found/comment',
                query: {
                    id
                }
            })
        },
        listItem(index){
            let getParams = window.localStorage.getItem('params');
            let jsonObj = JSON.parse(getParams);
            let listData = {
                m_id: jsonObj.m_id,
                token: jsonObj.token,
                sign: '1',
                timestamp: "22275555",
                id: index
            }
            this.$router.push({
                path: '/found/orderlist',
                query: {
                    listData
                }
            })
        }
    },

    created(){
        this.getOrderList();
    }
}
</script>
<style>
    .comment_service{
        width: 100%;
        height: 100%;
        padding-bottom:70px;
    }

    .comment_service ul li{
        padding: 8px;
        margin-bottom: 5px;
        margin-top:5px;
        background: #fff;
    }

    .comment_service .headPortrait{
        width: 30%;
        float: left;
    }

    .comment_service .headPortrait img{
        width: 80px;
        height: 80px;
        border-radius: 50%;
    }

    .comment_service .headPortrait span{
        display: block;
        margin-top: 10%;
        font-size:12px;
        width:80px;
        text-align:center;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap
    }

    .comment_service .accomplishInfo{
        width: 70%;
        float: left;
        text-align: left;
    }

     .comment_service .accomplishInfo div,
     .comment_service .accomplishInfo span
     {
         margin-bottom: 2%;
         font-weight: 400;
         font-size: 12px;
     }
    .accomplishInfo .order-detail{
        display:block;
        text-align:center;
        width:60px;
        height:20px;
        line-height:20px;
        border:1px solid #e0e0e0;
        border-radius:10px;
        margin-top:10px;
        float:left;
        margin-left:10px;
    }
    .accomplishInfo .order-detail.order{
        margin-left:60px;
    }

</style>





