<template>
  <div class="set">
    <div class="banner">
      <!-- <i></i> -->
      <img src="../../assets/bannar/bannar4.png" alt="">
    </div>
    <div class="tool">
      <ul class="clearfix">
        <!--
        <router-link to="/introduce">
        <li>
          <i>公司介绍</i>
          <span>></span>
        </li>
        </router-link>
        <router-link to="/team">
        <li>
          <i>团队介绍</i>
          <span>></span>
        </li>
        </router-link>
        <router-link to="/news">
        <li>
          <i>最新动态</i>
          <span>></span>
        </li>
        </router-link>
         -->
          <li @click="phoneCall">
            <i>电话客服</i>
            <span>></span>
          </li>
        <router-link to="/my/agreement">
          <li>
            <i>即刻行家政用户服务协议</i>
            <span>></span>
          </li>
        </router-link>
        <li @click="logOut">退出当前账户</li>
      </ul>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
export default {
  methods: {
    logOut() {
      MessageBox.confirm('确定退出?').then(action => {
        window.localStorage.removeItem('params');
          this.$router.push('/login');
      })
    },
    phoneCall(){
        /*
        MessageBox.confirm('拨打客服电话：023-67383851').then(action => {
            window.location.href = 'tel://023-67383851';
        })
        */
        MessageBox.confirm('拨打客服电话：13808867969').then(action => {
            window.location.href = 'tel://13808867969';
        })
    }
  }
};
</script>
<style lang="less">
.set {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  header {
    height: 7%;
    width: 100%;
    padding: 10px 0;
    background: #00c78c;
    color: #fff;
    text-align: center;
    font-weight: 600;
    font-size: 22px;
  }
  .mint-header {
    height: 47px;
    background: #00c78c;
  }
  .mint-header-title {
    color: #fff;
    font-size: 20px;
  }
  .mintui-back:before {
    content: "\E600";
    color: #fff;
  }
  .banner {
    height: 150px;
    width: 100%;
    img {
      width: 100%;
    }
  }
  .tool {
    /*margin-top: 5px;*/
    /*padding:0 8px;*/
    ul {
      li {
        font-size:14px;
        height: 40px;
        line-height: 40px;
        padding: 0 15px;
        background: #fff;
        margin-bottom: 5px;
        /*border-radius:5px;*/
        span {
          float: right;
          color: #e4e4e4;
        }
      }
      li:nth-last-child(1) {
        color: red;

      }
    }
  }
}
</style>



