<template>
  <div class="big_box">
    <div class="top_pic">
      <img src="../../assets/my/pic/banner.png" alt="">
    </div>
    <div class="centent">
      <ul>
        <li :model="payData" v-for="(item, index) in payData" :key='index'>
          <div class="content_right" :class="{active:index === num}" @click="tab(index,item.Id,item.cue)" data-value="item.Id">
              <span class="title">{{item.name}}</span>

              <span class="amount">充值金额：{{item.count}}元</span>

              <span class="span-top">赠{{item.add_money}}</span>
            <p class="warn-recharge">{{item.cue}}</p>
          </div>
        </li>
      </ul>
    </div>
    <div class="submit-div">
      <button class="pay_button" @click="sendData()">立即充值</button>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      payData: [],
      num: '',
      card_id:'',
      warnTitle:''
    };
  },
  methods: {
    sendData() {
      let getParams = window.localStorage.getItem("params");
      let jsonObj = JSON.parse(getParams);
      let card_id = this.card_id;

      if(!card_id){
        alert('请选择充值套餐')
          return false;
      }
      this.$router.push({
        path: "/my/paynext",
        query: {
          card_id: card_id,
          m_id: jsonObj.m_id,
          token: jsonObj.token,
          sign: 68,
          timestamp: 90
        }
      });
    },
    tab(index,id,warnTitle) {
        this.num = index;
        this.card_id = id;
        this.warnTitle = warnTitle;
    },
  },
  created() {
    let getParams = window.localStorage.getItem("params");
    let jsonObj = JSON.parse(getParams);

    if(jsonObj){
        let formData = {
            m_id: jsonObj.m_id,
            token: jsonObj.token,
            sign: "123",
            timestamp: "1122222"
        };
        this.$http.get('api_mobile/user/info/is_reg', { params: formData })
            .then(res => {
                if (res.data.status == 'n') {
                    window.localStorage.removeItem('params');
                    this.$router.push({
                        path: '/login',
                    })
                } else if (res.data.status == 'y') {
                    this.$http
                        .get("api_mobile/balance/recharge_list", { params: formData })
                        .then(res => {
                            this.payData = res.data.result;
                        });
                }
            })
    }else{
        this.$router.push({
            path: '/login',
        })
    }

  }
};
</script>

<style scoped>
.big_box {
  /* position: relative; */
  margin-bottom: 70px;
  background-color: #f2f2f2;
}

.top_pic {
  height: 25%;
}

.top_pic img {
  width: 100%;
}

.centent li {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right:5px;
  padding-left:10px;
  border-radius: 5px;
  background-color: #ffffff;
  float: left;
  width:50%;
  align-items: center;
}
.centent li:nth-child(2n) {
  padding-left:5px;
  padding-right:10px;
}
.centent li:first-child,.centent li:nth-child(2) {
 padding-top:20px;
}
.content_right {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  border:1px solid #e6e6e6;
  border-radius:5px;
  position: relative;
}
.content_right.active{
  border:1px solid #00c88d;
}

.content_right span {
  font-size: 12px;
}
.content_right .title{
 font-size:18px;
  color:#2c2c2c;
  margin-bottom: 8px;
}
.content_right .amount{
  font-size:12px;
  color:#999999;
}
.content_right .span-top {
  position:absolute;
  right:0;
  top:0;
  font-size:12px;
  padding:3px 20px;
  background-color:#00c88d;
  border-radius: 0 5px 0 20px;
  color:#fff;
}
.submit-div{
  padding-top:20px;
  clear: both;
  padding-bottom:70px;
}
 .warn-recharge{
  color:#f00;
  font-size:12px;
  line-height:12px;
  margin-top:10px;
}
.pay_button {
  padding: 0 25px;
  background-color: #00c78c;
  height: 36px;
  border-radius: 5px;
  display: block;
  width: 100%;
  color: #fff;

}
</style>


