<template>
  <div class="comment">
    <div class="personal_data clearfix">
      <div class="img">
        <img v-if="result.image" :src='"http://dev.jzfw.yaokexing.com"+result.image' alt="">
        <img v-else src="../../assets/tool/pic/保洁服务图片/家庭保洁.png" alt="">
      </div>
      <div class="title" :model="result">
        <p>订单号: {{result.order_no}}</p>
        <p>价格: {{ result.price_real }}</p>
        <p>服务项目: {{ result.service_name }}</p>
      </div>
    </div>
    <div class="main_t">
      <div class="discount">
          <div class="pp">
            <span class="title">支付金额</span><span>￥{{result.price_real}}</span>
          </div>
      </div>
    </div>
    <p class="pay-way">选择支付方式</p>
    <div class="main_t">
      <div class="discount">
        <div class="select-pay" v-if="result.service_name != '充值' && result.total_count > 0" >
          <img src="../../assets/my/pic/balance.png"></img>
          <span class="title">使用余额<label>可用余额{{result.total_count}}</label></span><img @click="change_star(type1)" v-if="type1" class="choise" src="../../assets/my/pic/选中.png"></img><img @click="change_star(type1)" v-else class="choise" src="../../assets/my/pic/no-select.png"></img>
        </div>
        <div class="select-pay">
          <img src="../../assets/my/pic/微信支付图标.png"></img>
          <span>微信支付</span>
          <img @click="change_star1(type2)" v-if="type2" class="choise" src="../../assets/my/pic/选中.png"></img><img @click="change_star1(type2)" v-else class="choise" src="../../assets/my/pic/no-select.png"></img>
        </div>
      </div>
    </div>
    <button class="r" size="large" @click="comment()"> 支付</button>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                currentdate: "",
                result: [],
                type1:'',
                type2:'',
                order_id:'',
                note:'',
                order_no:''

            };
        },
        mounted() {
            this.order_id=this.$route.query.id;
            this.sendData();
        },
        methods:{
            change_star(index){
               if(index){
                   this.type1 = false;
                   //this.type2 = true;
               }else{
                   this.type1 = true;
                   this.type2 = false;
               }
            },
            change_star1(index){
                if(index){
                    //this.type1 = true;
                    this.type2 = false;
                }else{
                    this.type1 = false;
                    this.type2 = true;
                }
            },
            sendData(){
                let getParams = window.localStorage.getItem('params')
                let jsonObj = JSON.parse(getParams)
                let order_id = this.order_id
                let formData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '123',
                    timestamp: '123313',
                    id:order_id,
                }
                this.$http.get('api_mobile/order/order/order_detail', {params:formData})
                    .then(res => {
                        if(res.data.status == 'y'){
                            console.log(res)
                            this.result = res.data.result;
                            this.order_no = res.data.result.order_no;
                            if(res.data.result.total_count > 0){
                                this.type1 = true;
                                this.type2 = false;
                            }else{
                                this.type1 = false;
                                this.type2 = true;

                            }
                        }else{

                        }
                    })
            },
            comment (){
                let getParams = window.localStorage.getItem('params');
                let jsonObj = JSON.parse(getParams);
                let type = '';
                if(!this.type1 && !this.type2){
                    alert('请选择支付方式')
                    return false;
                }else if(this.type1){
                    type=1
                }else{
                    type=2
                }
                if(type == 1){
                    let formData = {
                        m_id: jsonObj.m_id,
                        token: jsonObj.token,
                        sign: '123',
                        timestamp: '123313',
                        id:this.order_id,
                    }
                    this.$http.get('api_mobile/order/order/pay', {params:formData})
                        .then(res => {console.log(res)
                            if(res.data.status == 'y'){
                                this.result = res.data;
                                alert('支付成功')
                                this.$router.push({
                                    path: '/found'
                                })
                            }else if(res.data.info == 'pay'){
                                window.location.href = `http://dev.jzfw.yaokexing.com/api/pay/do_pay?order_no=` + this.order_no + `&m_id=${jsonObj.m_id}&token=${jsonObj.m_id}&sign=7&timestamp=8&id=`+this.order_id;
                            }else{
                                alert(res.data.info)
                            }
                        })
                }else{
                    window.location.href = `http://dev.jzfw.yaokexing.com/api/pay/do_pay?order_no=` + this.order_no + `&m_id=${jsonObj.m_id}&token=${jsonObj.m_id}&sign=7&timestamp=8&id=`+this.order_id;
                }

            }
        }
    };
</script>
<style lang="less">
  body{background: #f2f2f2;}
  .comment {
    width: 100%;
    overflow-y: auto;
    padding:0 10px;
    .personal_data {
      background: #fff;
      border-radius:5px;
      .img {
        float: left;
        img {
          padding-left: 16px;
          padding-top: 20px;
          padding-bottom:10px;
          width:80px;
        }
      }
      .main_title {
        clear:both;
        padding-top:5px;
        margin-left:10px;
        span{
          font-weight: 600;
          padding-left: 5px;
          line-height:20px;
          float:left;
          font-size:14px;
          padding-bottom:10px;
        }
        img{
          width:14px;
          height:14px;
          margin:3px;
          float:left
        }
      }
      .title {
        float:left;
        padding-top:24px;
        padding-left:10px;
        font-size: 14px;
        p{
          line-height:24px;
          font-family: '微软雅黑';
        }
      }
    }
    .main_t {
      width: 100%;
      position: relative;
      background: #fff;
      margin-top:10px;
      border-radius:5px;
      .discount {
        .select-pay{
          img{
            float:left;
            width:20px;
            margin:10px;
          }
          .choise{
            float:right;
            width:18px;
          }
          span {
            padding-left: 10px;
            line-height: 40px;
            height:40px;
            float:left;
            label{
              color:#ccc;
              font-size:12px;
              margin-left:5px;
            }
          }

        }

        div {
          line-height: 45px;
          height:45px;
          border-bottom: 1px solid #e6e6e6;
          padding-left:20px;
          .pp{
            span {
              font-size:14px;
              line-height:40px;
              float:right;
              margin-right:20px;
            }
          }
          .title{
            float:left;
          }
          img{
            width:20px;
            margin-top:10px;
            margin-left:15px;
          }
        }
      }
    }
    .pay-way{
      margin-top:20px;
      line-height: 20px;
      height:20px;
      }
    .r {
      padding: 0 25px;
      background-color: #00c78c;
      height: 38px;
      border-radius: 5px;
      width: 100%;
      color:#fff;
      margin-top:40px;
    }
  }
</style>


