<template>
        <div  class="mint-tabbar is-fixed footer-new" v-show='isShow'  :class="is_me" v-if="this.$route.meta.showFooter">
            <ul>
                <li v-if="selected == 'main'" :style="{backgroundImage: 'url(' + img4 + ')' }" @click="redirectType('main')">
                    <p>网站首页</p>
                </li>
                <li v-else :style="{backgroundImage: 'url(' + img1 + ')' }" @click="redirectType('main')">
                    <p>网站首页</p>
                </li>

                <li v-if="selected == 'tool'" :style="{backgroundImage: 'url(' + img4 + ')' }" @click="redirectType('tool')">
                    <p>预约服务</p>
                </li>
                <li v-else :style="{backgroundImage: 'url(' + img2 + ')' }" @click="redirectType('tool')">
                    <p>预约服务</p>
                </li>

                <li v-if="selected == 'my'" :style="{backgroundImage: 'url(' + img4 + ')' }" @click="redirectType('my')">
                    <p>个人中心</p>
                </li>
                <li v-else :style="{backgroundImage: 'url(' + img3 + ')' }" @click="redirectType('my')">
                    <p>个人中心</p>
                </li>
            </ul>
        </div>
</template>

<script>
    import Img1 from '@/assets/btn5.png'
    import Img2 from '@/assets/btn5.png'
    import Img3 from '@/assets/btn5.png'
    import Img4 from '@/assets/btn4.png'
    export default {
    data(){
        return{
            selected: '',
            img1:  Img1,
            img2:  Img2,
            img3:  Img3,
            img4:  Img4,
            fixed: false,
            is_me:'',
        }
    },
    computed: {
        isShow: function() {
            return true;
        }
    },
    methods:{
        redirectType(type){
            console.log(type)
            this.selected = type;
            if (this.selected == "main") {
                //一下一致用require的方式动态加载 ，require被webpack的AMD解析，所以动态没问题，这个地址 建议用方法生成 类似 getImgUrl(key),  key就是图标名称 直接return
                this.$router.push('/main');
            }
            if (this.selected == "tool") {
                this.$router.push('/tool');
            }
            if (this.selected == "my") {
                this.$router.push('/my')
            }
        }
    },
    mounted(){
        this.selected = this.$route.path.substr(1);

    },
    watch: {
        $route(to, from) {
            if (this.$route.path == "/main") {
                //一下一致用require的方式动态加载 ，require被webpack的AMD解析，所以动态没问题，这个地址 建议用方法生成 类似 getImgUrl(key),  key就是图标名称 直接return
                this.selected = 'main'
            }
            if (this.$route.path == "/tool") {
                this.selected = 'tool'
            }
            if (this.$route.path == "/my") {
                this.selected = 'my'
            }
        }
    }
}
</script>

<style scoped>
.mint-tabbar.is-fixed {
    /*height: 8.5%;*/
}
.footer-new ul{
    display:block;
    width:100%;
}
.footer-new ul li{
    display: block;
    height:55px;
    width:33.3%;
    float:left;
    text-decoration: none;
    font-size:12px;
}
.footer-new ul li p{
    color:#fff;
    line-height:55px;
}
.footer-new ul li img{
    margin-top:12px;
    width:20px;
}


</style>


