<template>
    <div class="yuyue">
        <div :model="contentData" ref="contentData" class="yuyue-contrain">
            <div class="img-detail-div">
                <img v-if='contentData.desc_image' :src='"http://dev.jzfw.yaokexing.com"+contentData.desc_image' alt="">
                <img v-else :src='"http://dev.jzfw.yaokexing.com"+contentData.show_image' alt="">
            </div>
            <div class="center-text">
                <span class="one-text">{{contentData.name}}</span>
                <span class="two-text">{{contentData.amount}}/{{contentData.type_name}}</span>
            </div>
            <div v-html="contentData.desc" class="imgsize"> 
            </div>
        </div>
    <mt-button type="primary" style="position:fixed;border-radius:0" class="yueButton" size="large" @click='determine()'>立即预约</mt-button>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                contentData: {
                    show_image:'',
                },
            }
        },
        methods:{
            determine(){
                let getParams = window.localStorage.getItem('params');
                if(getParams == null){
                    this.$router.push({
                        path: 'login',
                    })
                }else{
                    let _this = this
                    let jsonObj = JSON.parse(getParams);
                    let id =  _this.$route.query.id;
                    let localData = {
                        m_id: jsonObj.m_id,
                        token: jsonObj.token,
                        sign: 1,
                        timestamp: 3444443,
                    }
                    this.$http.get('api_mobile/user/info/is_reg', { params: localData })
                    .then(res => {
                        if (res.data.status == 'n') {
                            window.localStorage.removeItem('params');
                            this.$router.push({
                                path: 'login',
                            })
                        } else if (res.data.status == 'y') {
                            this.$router.push({
                                path: 'yunext',
                                query: {
                                    id: id
                                }
                            })
                        }
                    })
                }
            }
        },
        created(){
            let id = this.$route.query.id
            this.$http.get('api_mobile/cat/info?id='+id)
                .then(res => {
                    this.contentData = res.data.result
            })

            setTimeout(function(){
                var element = document.querySelectorAll(".imgsize img")
                if (element.length > 0) {
                    for (let i = 0; i < element.length; i++) {
                        console.log(element[i].src)
                        if(!/^dev.jzfw.yaokexing.com$/.test(element[i].src)){
                            element[i].src = 'http://dev.jzfw.yaokexing.com/uploads'+(element[i].src.split("/uploads"))[1]
                        }

                    }

                }
            },500)
        }
    }
</script>

<style>
.yuyue{
    
}

img{
   width:100%; 
}
.yuyue-contrain{
    margin-bottom:100px;
}
.img-detail-div{
    height:200px;
    overflow:hidden;
}

.yueButton {
    position: fixed;
    bottom: 55px;
    margin: 0 auto;
}
.center-text{
    margin:10px;
}
.center-text span{
    display: inline-block;
    /*float: left;*/
}
.center-text .one-text{
    font-weight: bold;
    font-size: 17px;
}
.center-text .two-text{
    margin-left: 10px;
    margin-top: 5px;
    font-size: 12px;
    /*color: #aeaeae;*/
    color:#ff0000;
}
.imgsize{
    padding-bottom:100px;
}
.imgsize img{
    width: 100% !important;
    height: 100% !important;    
}

</style>


