<template>
  <div class="address_list">
    <!-- <mt-header fixed title="服务地址">
      <router-link to="/my/site/addsite" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header> -->
    
    <div class="banner" v-show="isShow">
      <!-- <i></i> -->
      <img src="../../assets/my/pic/banner.png" alt="">
    </div>
    <div class="list">
      <ul>
        <li v-for="(item, index) in result" :key="index">
          <p class="clearfix">
            <span class="left">{{ item.full_name }}</span>
            <span class="right">{{ item.tel }}</span>
          </p>
          <p class="address">{{ item.province }}{{ item.city }}{{ item.area }}{{ item.address }}</p>
          <div class="address_b">
            <p class="btn">
              <span @click="editAddress(item.id)">编辑</span>
              <span @click="delAddress(item.id)">删除</span>
            </p>
            <!-- <mt-checklist :max="2" align="left" class="choose" v-model="value" :options="['默认地址']">
            </mt-checklist> -->
            <label for="" @click="radios()"><input type="radio" v-model="radio" :value="item.id">默认地址</label>
          </div>
        </li>
      </ul>
    </div>
    <div class="btn">
      <mt-button type="default" @click="addSite">新增服务地址</mt-button>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui';
export default {
  data() {
    return {
      radio: '',
      id: "",
      currentdate: "",
      value: '',
      result: [],
      isShow: false
    };
  },
  created() {
    this.getNowFormatDate();
    this.getAddressList();
  },
  methods: {
    radios(){
      let getParams = window.localStorage.getItem('params');
      let jsonObj = JSON.parse(getParams);
      console.log(this.radio)
      let edList ={
        id : this.radio,
        is_default: 1,
        m_id: jsonObj.m_id,
        token: jsonObj.token,
        sign: "jhj",
        timestamp: this.currentdate,
      }
      this.$http.get('api_mobile/user/address/edit', { params: edList})
      .then(res => {
      })
    },
    // 编辑地址
    editAddress(index){
      this.$router.push({
        path: "/my/site/addsite",
        query: {
          id: index
        }
      })
    },
    addSite() {
        console.log(this.$route.query.url)
        if(this.$route.query.url){
            this.$router.push({
                path: "/my/site/addsite",
                query: {
                    url: this.$route.query.url
                }
            });
        }else{
            this.$router.push("/my/site/addsite");
        }

    },
    getNowFormatDate() {
      let date = new Date();
      let seperator = "-";
      let month = date.getMonth() + 1;
      let strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.currentdate =
        date.getFullYear() + seperator + month + seperator + strDate;
    },
    getAddressList() {
      let getParams = window.localStorage.getItem('params');
      let jsonObj = JSON.parse(getParams);
      let paramsData = {
        m_id: jsonObj.m_id,
        token: jsonObj.token,
        sign: "jhj",
        timestamp: this.currentdate
      };
      this.$http
        .get(this.$api.addressList, { params: paramsData })
        .then(res => {
          console.log(res)
          if (res.data.result.length > 0) {
            this.result = res.data.result;
            for( let i=0; i<=this.result.length; i++){
              if(this.result[i].is_default == '1'){
                this.radio = this.result[i].id
              }
            }
          } else if(res.data.result.length <= 0){
            this.isShow = true;
          }
        })
        .catch(err => {
          console.log("getAddressList有异常", err);
        });
    },
    delAddress(index) {
      let getParams = window.localStorage.getItem('params');
      let jsonObj = JSON.parse(getParams);
      let delParams = {
        m_id: jsonObj.m_id,
        token: jsonObj.token,
        sign: "jhj",
        timestamp: this.currentdate,
        id: index
      };
      MessageBox.confirm('确定删除地址?').then(action => {
         this.$http.get(this.$api.addressDelete, { params: delParams }).then(res => {
          if (res.data.status = 'y') {
            window.location.reload()
          }
        });
      })
    }
  }
};
</script>
<style lang="less">

 .banner {
   width: 100%;
   img {
     width: 100%;
   }
 }


 .btn {
   .mint-button--normal {
    color: #fff;
    width: 100%;
    background: #00c78c;
    margin-bottom: 50px;
   }
 }

.address_list {
  width: 100%;
  height: 93.8%;
  // margin-bottom: 8.5%;
  overflow-y: scroll;
  background: #f0f0f0;
  .mint-header {
    height: 47px;
    background: #00c78c;
  }
  .mint-header-title {
    color: #fff;
    font-size: 16px;
  }
  .mintui-back:before {
    content: "\E600";
    color: #fff;
  }
  .list {
    padding: 3% 2%;
    ul {
      // margin-bottom: 6%;
      li {
        font-size: 15px;
        line-height: 40px;
        background: #fff;
        margin-bottom: 3%;
        padding: 0 2%;
        border-radius: 5px;
        p:nth-child(1) {
          font-weight: 600;
        }
        p {
          background: #fff;
        }
        .address {
          color: #666;
          font-size: 13px;
          border-bottom: 1px solid #f7f7f7;
        }
        .address_b {
          font-size: 14px;
          position: relative;
          .btn {
            top: 0%;
            right: 0%;
            z-index: 1;
            position: absolute;
            span {
              color: #999;
            }
            span:nth-child(2) {
              padding-left: 5px;
            }
          }
          .choose {
            .mint-cell-wrapper {
              padding: 0;
            }
            .mint-checklist-label {
              display: block;
              padding: 0;
              color: #999;
              font-size: 14px;
            }
            .mint-checkbox.is-right {
              float: right;
              padding-left: 5px;
            }
            .mint-checkbox-input:checked + .mint-checkbox-core {
              background: #08d5a7;
              border-color: #08d5a7;
            }
            .mint-cell-title {
              // -webkit-box-flex: 1;
              // flex: 1;
            }
            .mint-cell:last-child {
              background-size: 100% 0px;
            }
            .mint-cell {
              min-height: 40px;
            }
          }
        }
      }
      li:nth-last-child(1) {
        margin-bottom: 6%;
      }
    }
  }
}
</style>



