<template>
    <div class="order_box">
        <div class="list_order" :model="orderDetail">
            <div class="list_first order-detail"><p>订单号&nbsp;&nbsp;&nbsp;</p><span>{{orderDetail.order_no}}</span><span style="float:right; color:red">{{orderDetail.order_status}}</span></div>
            <div  class="clearfix order-detail"><p>预约项目</p><span>{{orderDetail.service_name}}</span></div>
            <div  class="clearfix order-detail"><p>服务人员</p><span>{{orderDetail.real_people}}</span></div>
            <div  class="clearfix order-detail"><p>服务单价</p><span>{{orderDetail.amount}}/{{orderDetail.type_name}}</span></div>
            <div  class="clearfix order-detail"><p>服务时间</p><span>{{orderDetail.completetime}}</span></div>
            <div  class="clearfix order-detail"><p>服务地址</p><span>{{orderDetail.prov}}{{orderDetail.city}}{{orderDetail.area}}{{orderDetail.address}}</span></div>
            <div  class="clearfix order-detail"><p>支付金额</p><span>{{orderDetail.price_real}}</span></div>
            <div  class="clearfix order-detail"><p>预约人&nbsp;&nbsp;&nbsp;</p><span>{{orderDetail.full_name}}</span></div>
            <div  class="clearfix order-detail"><p>预约电话</p><span>{{orderDetail.phone}}</span></div>
            <div v-if="orderDetail.order_status == '服务中'" class="clearfix order-detail"><p>支付订单</p><span @click="payorder(orderDetail.id)" style="color:red">支付</span></div>
            <div v-if="orderDetail.order_status == '待评价'" class="clearfix order-detail"><p>支付订单</p><span @click="commend(orderDetail.id)" style="color:red">评价</span></div>

        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                orderDetail: {},
            }
        },
        methods : {
            payorder(e) {
                this.$router.push({
                    path: '/found/payorder',
                    query: {
                        id:e
                    }
                })
            },
            commend(e){
                this.$router.push({
                    path: '/found/comment',
                    query: {
                        e
                    }
                })
            }
        },
        created(){
            let listData = this.$route.query.listData
            this.$http.get('api_mobile/order/order/order_detail', {params:listData})
            .then(res =>{
                console.log(res)
                this.orderDetail = res.data.result
            })
        }
    }
</script>

<style scoped>

.order_box{
    width: 100%;
    height: 100%;
    font-size:12px;
    background-color: #f0f0f0;
}
.list_order{
    position: absolute;
    margin: 10px;
    background-color: #ffffff;
    border-radius: 5px;
    padding: 10px;
}
.list_order p{
    color: #c6c6c6;
}

.list_order div{
    line-height: 35px;
}
.list_order .order-detail{
    float:left; width:100%
}
.list_order .order-detail p{
     float:left;
 }
.list_order .order-detail span{
    margin-left:10px;
}
.list_first{
    border-bottom:1px soild #ff0000;
}


</style>


