<template>
  <div class="jifen">
    <!-- <header>东庭家政</header> -->
    <div class="personal_data clearfix">
      <p>{{date1}}  <img @click="open('picker1')" src="../../assets/my/pic/picker.png"/><!--<input v-else type="text" v-model="date1"  @click="open('picker1')" class="found_but">-->
        <span class="no-date">
        <mt-datetime-picker ref="picker1" type="date"
                            v-model="birthday"
                            year-format="{value} 年"
                            month-format="{value} 月"
                            @confirm="handleChange">
        </mt-datetime-picker>
        </span>
      </p>

    </div>
    <div class="main_t" v-if="is_show">
      <div class="discount"  v-for="item in list" :key="item.id">
        <div class="fill-li-div">
          <span class="title" v-if="item.note">{{item.note}}</span>
          <span class="title" v-else>充值</span>
          <span>{{item.addtime}}</span>
        </div>
        <div class="fill-li-div fill-li-right">
          <span v-if="item.jifen > 0">+{{item.jifen}}积分</span>
          <span v-else>{{item.jifen}}</span>
        </div>
      </div>
    </div>
    <div class="main_t" v-else>
      <div class="black-detail">
        <img src="../../assets/aa.gif">
        <p>咦 ~，没有数据呢 ~</p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                currentdate: "",
                result: [],
                date1: '',
                //show: true,
                birthday:'',
                list:[],
                is_show:0
            };
        },
        created() {
            this.date1 = new Date().getFullYear()+'年'+ (new Date().getMonth()+1)+'月'      //获取当前月份(0-11,0代表1月)
            this.date = new Date().getFullYear()+'-'+ (new Date().getMonth()+1)
            this.getList()

        },
        methods: {
            open(picker) {
                this.$refs[picker].open();
            },
            handleChange(value) {
                let date = new Date(value)
                let year = date.getFullYear();//年
                let month = date.getMonth() + 1;//月份（月份是从0~11，所以显示时要加1）
                // let day = date.getDate();//日期
                //  this.date1 = year + '-' + month + '-' + day;
                this.date1 = year + '年' + month+'月';
                this.date = year + '-' + month;
                //重新请求参数
                this.getList()
            },
            openPicker() {
                this.$refs.picker.open();
            },
            getList(){
                let date = this.date;
                let getParams = window.localStorage.getItem('params');
                if (getParams == null) {
                    this.$router.push({
                        path: 'login',
                    })
                } else {
                    let _this =this
                    let jsonObj = JSON.parse(getParams);
                    let id = _this.$route.query.id;
                    let localData = {
                        m_id: jsonObj.m_id,
                        token: jsonObj.token,
                        sign: 1,
                        timestamp: 3444443,
                        date:date
                    }
                    this.$http.get('api_mobile/balance/jifen_list', { params: localData })
                        .then(res => {
                            if (res.data.status == 'n') {
                                window.localStorage.removeItem('params');
                                this.$router.push({
                                    path: 'login',
                                })
                            } else if (res.data.status == 'y') {
                                console.log(res.data.result)
                                this.list = res.data.result;
                                if(res.data.result.length){
                                    this.is_show = 1
                                }else{
                                    this.is_show = 0
                                }
                            }
                        })
                }
            }
        }
    };
</script>

<style lang="less">
  body{background:#f2f2f2;}
  .jifen {
    width: 100%;
    background: #f2f2f2;
    overflow-y: auto;
    padding:0 10px;
    padding-bottom:70px;
    .personal_data {
      background: #fff;
      height:50px;
      line-height:50px;
      padding:0 20px;
      border-radius:5px;
      font-size: 14px;
      img{
        width:20px;
        float:right;
        margin-top:15px;
      }
    }
    .found_but{
      float: right;
      line-height: 50px;
      text-align: right;
    }
    .main_t {
      width: 100%;
      background: #fff;
      margin-top:8px;
      border-radius:5px;
      .discount {
        height: 60px;
        border-bottom: 1px solid #e6e6e6;
        padding-left: 20px;
        .fill-li-div {
          float:left;
          span {
            font-size: 12px;
            display:block;
            line-height:20px;
            color:#666;
          }
          .title{
            margin-top:10px;
            font-size:14px;
            color:#2c2c2c;
          }
        }
        .fill-li-right{
          float:right;
          margin-right:20px;
          span{
            color:#00c88d;
            font-size:18px;
            line-height:60px;
          }
        }
      }
    }
  }
  .no-date .picker-slot.picker-slot-center:nth-child(3){
    display:none;
  }

  .black-detail{
    padding-top:100px;
    background: #f2f2f2;
    img{
      width:150px;
      margin: auto;
      display: block;
    }
    p{
      margin-top:40px;
      text-align:center;
      color:#999
    }
  }

</style>


