<template>
  <div id="app">
    <!--<p class="go-back" @click="$router.back(-1)"><i></i></p>-->
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
      <div  v-if="!this.$route.meta.showback" class="div-go-back" @click="$router.back(-1)">返回</div>
    <bar class='footer'></bar>
  </div>
</template>


<script>
import Bar from './components/button'
export default {
  name: 'App',
  components:{
    Bar
  },
   mounted(){
        var params = {'url':window.location.href.split('#')[0]};
       this.$http.get('api_mobile/wechat/wx_config',{ params: params })
           .then(res => {
               console.log(res)

               wx.config({
                   //debug : true,
                   appId: res.data.result.appid, // 必填，公众号的唯一标识
                   timestamp: res.data.result.timestamp , // 必填，生成签名的时间戳
                   nonceStr: res.data.result.noncestr, // 必填，生成签名的随机串
                   signature: res.data.result.signature,// S必填，签名
                   jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage','onMenuShareQQ','onMenuShareQZone','onMenuShareWeibo'] ,// 必填，需要使用的JS接口列表
               });

               wx.ready(() =>{
                   var shareData64 = {
                       title: '即刻行家政',
                       desc: '即刻行家政欢迎您！',
                       //link: 'http://dev.mjkx.yaokexing.com',
                       link:window.location.href.split('#')[0],
                       imgUrl: 'http://dev.jzfw.yaokexing.com/public/images/Icon2.png',
                       trigger: function (res) { },
                       success: function (res) {
                           console.log('分享成功');
                          // alert('分享成功')
                       },
                       cancel: function (res) {
                           console.log(res)
                           alert(res)
                       },
                       fail: function (res) {
                           console.log(JSON.stringify(res))
                           alert(res)
                       }
                   };
                   //wx.updateTimelineShareData(shareData64);
                   wx.onMenuShareAppMessage(shareData64);
                   wx.onMenuShareTimeline(shareData64);
                   wx.onMenuShareQQ(shareData64);
                   wx.onMenuShareWeibo(shareData64);
                   wx.onMenuShareQZone(shareData64)
               })
           })
   }
}
</script>

<style>
#app {
  height: 100%;
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */
}
  .go-back{
    position:fixed;
    height:25px;
    top:0;
    width:100%;
    background:#000;
    opacity:0.3;
  }
  .go-back i{
    display: inline-block;
    width: 10px;
    height: 10px;
    vertical-align: -2px;
    border-top: 2px solid #ddd;
    border-right: 2px solid #ddd;
    -webkit-transform: rotate(-135deg);
    margin-left:10px;
  }
.div-go-back{
  z-index:1000;
  position:fixed;
  height:50px;
  width:50px;
  bottom:100px;
  right:0;
  text-align:center;
  line-height:50px;
  border-radius:50px;
  font-size:12px;
  color:#fff;
  cursor:pointer;
  background:#0cd17b;
}
</style>



