<template>
    <div class="hello-c">
       <div class="alert-comment">
           <div class="comment-send"><span  @click="sendComment()">发表</span></div>
           <div class="word-comment"><textarea placeholder="这一刻的想法..." v-model="note"></textarea></div>
           <div class="img-list">
               <div v-if="imgNum >=0" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file1" data-value="1"  @change='onUpload'><img v-if="imgNum >=1" class="" :src='"http://dev.jzfw.yaokexing.com"+file1'/></div></div>
               <div v-if="imgNum >=1" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file2" data-value="2"  @change='onUpload'><img v-if="imgNum >=2" class="" :src='"http://dev.jzfw.yaokexing.com"+file2'/></div></div>
               <div v-if="imgNum >=2" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file3" data-value="3"  @change='onUpload'><img v-if="imgNum >=3" class="" :src='"http://dev.jzfw.yaokexing.com"+file3'/></div></div>
               <div v-if="imgNum >=3" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file4" data-value="4"  @change='onUpload'><img v-if="imgNum >=4" class="" :src='"http://dev.jzfw.yaokexing.com"+file4'/></div></div>
               <div v-if="imgNum >=4" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file5" data-value="5"  @change='onUpload'><img v-if="imgNum >=5" class="" :src='"http://dev.jzfw.yaokexing.com"+file5'/></div></div>
               <div v-if="imgNum >=5" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file6" data-value="6"  @change='onUpload'><img v-if="imgNum >=6" class="" :src='"http://dev.jzfw.yaokexing.com"+file6'/></div></div>
               <div v-if="imgNum >=6" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file7" data-value="7"  @change='onUpload'><img v-if="imgNum >=7" class="" :src='"http://dev.jzfw.yaokexing.com"+file7'/></div></div>
               <div v-if="imgNum >=7"  class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file8" data-value="8"  @change='onUpload'><img v-if="imgNum >=8" class="" :src='"http://dev.jzfw.yaokexing.com"+file8'/></div></div>
               <div v-if="imgNum >=8" class="img-div"><div class="img-upload"><span>+</span><input type="file" value=""  id="file9" data-value="9"  @change='onUpload'><img v-if="imgNum >=9" class="" :src='"http://dev.jzfw.yaokexing.com"+file9'/></div></div>
           </div>
       </div>
        <div class="mint-tabbar is-fixed footer-new">
            <ul>
                <li @click="redirectEdit()">
                    <img src="../assets/allcomment/wxz4.png"/>
                    <p>首页</p>
                </li>
                <li>
                    <p class="active-edit"><img src="../assets/allcomment/xz6.png"/></p>
                </li>
                <li @click="redirectMe()">
                    <img src="../assets/allcomment/wxz5.png"/>
                    <p>我的</p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { domain } from '../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            imgNum:0,
            file1:'',
            file2:'',
            file3:'',
            file4:'',
            file5:'',
            file6:'',
            file7:'',
            file8:'',
            file9:'',
            auth_id:'',
            note:'',
        }
    },
    methods:{
        redirectEdit(){
            this.$router.push({
                path: '/talk',
                query: {
                    openid: this.$route.query.openid
                }
            })
        },
        redirectMe(){
            this.$router.push({
                path: '/mytalk',
                query: {
                    openid: this.$route.query.openid
                }
            })
        },
        sendComment(){
            let formData = {
                'file1' :this.file1,
                'file2' :this.file2,
                'file3' :this.file3,
                'file4' :this.file4,
                'file5' :this.file5,
                'file6' :this.file6,
                'file7' :this.file7,
                'file8' :this.file8,
                'file9' :this.file9,
                'note'  :this.note,
                'auth_id'  :this.auth_id,
            }

            this.$http.get('api_mobile/cat/send_message',{ params:formData})
                .then(res => {
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        console.log(res);
                        this.$router.back()
                    }
                })
        },
        onUpload(e,){
            console.log(e);
            var formData = new FormData();
            formData.append('file', e.target.files[0]);
            var img_id = e.target.id;
            this.imgNum = e.target.dataset.value;
            console.log(img_id);
           // formData.append('type', 'test');
            let config = {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }
            var _this = this;
            this.$http.post('/api_mobile/cat/upload', formData, config).then(function (res) {
                if (res.status === 200) {
                    /*这里做处理*/
                    if (res.data.status === "success"){
                        console.log(res.data.result.url)
                        //this.img_id = res.data.result.url;
                        if(img_id == 'file1'){
                            _this.file1 = res.data.result.url;
                        }else if(img_id == 'file2'){
                            _this.file2 = res.data.result.url;
                        }else if(img_id == 'file3'){
                            _this.file3 = res.data.result.url;
                        }else if(img_id == 'file4'){
                            _this.file4 = res.data.result.url;
                        }else if(img_id == 'file5'){
                            _this.file5 = res.data.result.url;
                        }else if(img_id == 'file6'){
                            _this.file6 = res.data.result.url;
                        }else if(img_id == 'file7'){
                            _this.file7 = res.data.result.url;
                        }else if(img_id == 'file8'){
                            _this.file8 = res.data.result.url;
                        }else if(img_id == 'file9'){
                            _this.file9 = res.data.result.url;
                        }

                        let element = document.querySelectorAll(".img-div .img-upload img");
                        if(element.length > 0){
                            for(let i=0; i < element.length ;i++){
                                console.log(element[i].height)
                                console.log(element[i].width)
                                setTimeout(function() {
                                    if(element[i].height < element[i].width){
                                        element[i].style.height = '100%';
                                        element[i].style.width = 'auto';
                                    }
                                },500)
                            }
                        }
                    }

                }
            })

        }
    },
    mounted(e){


        let openid = this.$route.query.openid
        if(!openid){
            window.location.href =  'http://dev.jzfw.yaokexing.com/api_mobile/cat/getuserinfo';
        }else{
            this.auth_id = openid;
        }



    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello-c{
    background-color: #f2f2f2;
    margin-bottom:80px;
}
.alert-comment{
    position:absolute;
    width:100%;
    height:100%;
    background:#fff;
    top:0;
}
.alert-comment .comment-send{
    text-align:right;
    font-size:12px;
    margin: 10px;
}
.alert-comment .comment-send span{
    color:#00C88D;
}
.alert-comment .word-comment{
    margin:10px;
}
.alert-comment .word-comment textarea{
    width:100%;
    height:80px;
    overflow:hidden;
    resize: none
}
.alert-comment .img-list .img-div{
    position: relative;
    /*display: inline-block;*/
    float:left;
    width:33.3%;
    height:100px;
    padding: 10px 0;
}
.alert-comment .img-list .img-div img{
    position: absolute;
    /*width:80px;*/
    width:100%;
    /*height:80px;*/
    top:0;
}
.alert-comment .img-list .img-upload{
   margin:auto;
    width:80px;
    height:80px;
    position:relative;
    overflow: hidden;
}
.alert-comment .img-list .img-upload span{
   width:100%;
    height:100%;
    display:block;
    text-align:center;
    line-height:80px;
    font-size:40px;
    background: #eee;
    color:#ddd;
}
.alert-comment .img-list .img-upload input{
    position: absolute;
    right: 0px;
    top: 0px;
    height:80px;
    width:80px;
    opacity:0;
}
.footer-new ul{
    display:block;
    width:100%;
}
.footer-new ul li{
    display: block;
    height:55px;
    width:33.3%;
    float:left;
    text-decoration: none;
    font-size:12px;
}
.footer-new ul .active p{
    color:#00C88D;
}

.footer-new ul li img{
    margin-top:12px;
    width:20px;
}
.footer-new ul li .active-edit{
    height:42px;
    width:76px;
    background:#00C88D;
    margin:auto;
    margin-top:7px;
    border-radius:10px;
}
</style>



