<template>
    <div class="news">
        <div class="img">
            <!--<img src="../assets/bannar/bannar2.png"/>-->
            <img :src='"http://dev.jzfw.yaokexing.com"+detail.image'/>
        </div>
       <div class="new-detail" v-html="detail.desc">
       </div>
    </div>
</template>
<script>
    import { domain } from '../api/api_config'
    export default {
        name: 'HelloWorld',
        data() {
            return {
                detail:[]

            }
        },
        mounted(e){
            let formData = {
                id:this.$route.query.id
            }
            this.$http.get('api_mobile/cat/new_detail',{params:formData})
                .then(
                    res => {
                        console.log(res)
                        if (res.data.status == 'n') {
                            alert(res.data.info)
                        }else if(res.data.status == 'y'){
                            this.detail = res.data.result;
                        }
                    }
                )

        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .news{
        padding-bottom:60px;
    }
    .news img{
        width:100%;
    }
    .new-detail{
        margin:10px;
    }
    .new-detail p{
        text-align: justify;
    }


</style>



