<template>
    <div class="introduce">
        <div class="img">
            <img src="../assets/bannar2.png"/>
        </div>
        <div class="introduce-title">
                <p class="title">成为个人客户</p>
            <p class="title-en">Become an personal customer</p>
            <div class="active-b">
               <div class="message">
                   <div class="list"><label>联系人姓名:</label><input type="text" placeholder="请输入联系人姓名" v-model="form.full_name"/></div>
                   <div class="list"><label>联系人手机:</label><input type="text" placeholder="请输入联系人手机" v-model="form.tel"/></div>
                   <!--<div class="list"><label>住所所在地:</label><input type="text" placeholder="请输入住所所在地" v-model="form.province"/></div>-->
                   <mt-cell class="list" title="住所所在地" :value="areaString" is-link @click.native="handlerArea"></mt-cell>
                   <mt-popup v-model="areaVisible" class="area-class" position="bottom">
                       <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
                   </mt-popup>
                   <mt-popup v-model="streetVisible" class="area-class" position="bottom">
                       <mt-picker :slots="slotstree" @change="onStreeChange"></mt-picker>
                   </mt-popup>
                   <div class="list"><label>住所详细地址:</label><input type="text" placeholder="请输入住所详细地址" v-model="form.address"/></div>
                   <div class="buttom" @click="accomplish">一键注册</div>
               </div>
        </div>
        </div>
        <div class="footer">
            <ul class="clearfix">
                <li v-for="(item, index) in sundata" :key='index'@click="listnum(item.id)">
                    <a href="javascript:;"><img :src='"http://dev.jzfw.yaokexing.com"+item.show_image' alt=""></a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import data from "../api/data2.json";
    let index = 0;
    let index2 = 0;
    let index3 = 0;
    let province = Object.keys(data);
    // 初始化市
    let _y = Object.values(data);
    let _m = _y.map(res => {
        return Object.keys(res);
    });
    let city = _m[index];
    // 初始化区
    let _c = Object.values(data);
    _c = _c.map(res => {
        return Object.values(res);
    });
    let __d = Object.values(_c[index]);
    let area = Object.keys(__d[index]);
    // 初始化街
    let __a = Object.values(__d[0]);
    let street = __a[0];
export default {
    name: 'index',
    data() {
        return {
            swipedata: [],
            cleardata: [],
            subdata: [],
            sundata: [],
            form: {
                province: "",
                city: "",
                area: "",
                address: "",
                company: '',
                scale: "",
                full_name: "",
                tel: ""
            },
            areaVisible: false,
            streetVisible: false,
            areaString: "请选择",
            streetString: "请选择",
            slots: [
                {
                    flex: 1,
                    values: province,
                    className: "slot1",
                    textAlign: "center"
                },
                {
                    divider: true,
                    content: "-",
                    className: "slot2"
                },
                {
                    flex: 1,
                    values: city,
                    className: "slot3",
                    textAlign: "center"
                },
                {
                    divider: true,
                    content: "-",
                    className: "slot4"
                },
                {
                    flex: 1,
                    values: area,
                    className: "slot5",
                    textAlign: "center"
                }
            ],
            slotstree: [
                {
                    flex: 1,
                    values: street,
                    className: "slot1",
                    textAlign: "center"
                }
            ]

        }
    },
    methods: {
        check(){

            if(this.checkbox == true){
                this.is_default = 0;
                this.checkbox = false;
            }else{
                this.is_default = 1;
                this.checkbox = true;
            }
            console.log(this.checkbox)
        },
        handlerArea() {
            this.areaVisible = true;
        },
        onValuesChange(picker, values) {
            this.form.province = values[0];
            this.form.city = values[1];
            this.form.area = values[2];
            index = province.indexOf(values[0]);
            index2 = _m[index].indexOf(values[1]);
            if (index >= 0) {
                picker.setSlotValues(1, _m[index]);
            }
            if (index >= 0 && index2 >= 0) {
                let __d = Object.values(_c[index]);
                let area = Object.keys(__d[index2]);
                index3 = area.indexOf(values[2]);
                picker.setSlotValues(2, area);
                this.areaString = values.join(",");
                if (index3 >= 0) {
                    let __a = Object.values(__d[index2]);
                    let street = __a[index3];
                    this.slotstree[0].values = street;
                } else {
                    this.$set(this.slotstree[0].values, []);
                    this.streetString = "无可选街道";
                }
            }
        },
        onStreeChange(picker, values) {
            this.streetString = values.join(",");
        },
        accomplish() {
            let paramsData = {
                username: this.form.full_name,
                province: this.form.province,
                city: this.form.city,
                area: this.form.area,
                address: this.form.address,
                scale: this.form.scale,
                tel: this.form.tel,
                company: '',
                is_default: 1,
                is_big: 0,
                id: this.$route.query.id
            };
            this.$http
                .get(this.$api.addUser, {
                    params: paramsData
                })
                .then(res => {
                    if (res.status == 200) {
                       // this.$router.push("/my/addresslist");
                       // this.form = {};
                        if(res.data.status == 'y'){
                            this.form = {};
                        }
                        console.log(res);
                    }
                });
        },
        getNowFormatDate() {
            let date = new Date();
            let seperator = "-";
            let month = date.getMonth() + 1;
            let strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            this.currentdate =
                date.getFullYear() + seperator + month + seperator + strDate;
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .introduce{
        padding-bottom:60px;
    }
    .introduce img{
        width:100%;
    }
    .introduce-title .title{
       color: #00C88D;
       font-size:15px;
        text-align:center;
        margin-top:15px;
    }
    .introduce-title .title-en{
        color: #000;
        font-size:8px;
        text-align:center;
        margin-bottom:18px;
    }
    .introduce-title .active-b{
        margin:5px;
    }
    .introduce-title .active-b p{
       line-height:20px;
        font-size:12px;
        text-align:left;
    }
    .message{
        margin-top:10px;
    }
    .message .list{
        height:40px;
        line-height:40px;
        border-bottom:1px solid #E6E6E6;
        font-size:12px;
        color:#000;
    }
    .message .list label{
        text-align: left;
        display:inline-block;
        width:30%;
        padding-left:10px;
    }
    .message .list input{
      background: transparent;
    }
    .message .buttom{
        background:#00C88D;
        height:40px;
        line-height:40px;
        width:50%;
        margin: 20px auto;
        color:#fff;
        font-size:15px;
        text-align:center;
        cursor:pointer;
     }
    .clear{clear:both}



</style>



