<template>
  <div class="add_site">
    <div class="banner">
      <img src="../../assets/my/pic/banner.png" alt="">
    </div>
    <div class="message">
      <mt-field label="姓名" placeholder="请输入联系人姓名" v-model="form.full_name"></mt-field>
      <mt-field label="联系电话" placeholder="请输入联系电话" v-model="form.tel"></mt-field>
      <mt-field label="服务面积" placeholder="请正确填写房屋平方" v-model="form.scale"></mt-field>
      <mt-cell title="详细地址" :value="areaString" is-link @click.native="handlerArea"></mt-cell>
      <mt-popup v-model="areaVisible" class="area-class" position="bottom">
        <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
      </mt-popup>
      <mt-popup v-model="streetVisible" class="area-class" position="bottom">
        <mt-picker :slots="slotstree" @change="onStreeChange"></mt-picker>
      </mt-popup>
      <mt-field placeholder="请输入详细地址信息" v-model="form.address"></mt-field>
      <div class="default-set">
        <div>
          <span>设为默认地址：</span>
          <label for="" @click="check()" style="float:right"><img v-if="checkbox" class="choise" src="../../assets/my/pic/选中.png"></img><img v-else class="choise" src="../../assets/my/pic/no-select.png"></img></label>
        </div>
       </div>
    </div>
    <div class="btn">
      <mt-button type="default" @click="accomplish">完成</mt-button>
    </div>
  </div>
</template>
<script>
import data from "../../api/data2.json";
let index = 0;
let index2 = 0;
let index3 = 0;
let province = Object.keys(data);
// 初始化市
let _y = Object.values(data);
let _m = _y.map(res => {
  return Object.keys(res);
});
let city = _m[index];
// 初始化区
let _c = Object.values(data);
_c = _c.map(res => {
  return Object.values(res);
});
let __d = Object.values(_c[index]);
let area = Object.keys(__d[index]);
// 初始化街
let __a = Object.values(__d[0]);
let street = __a[0];
export default {
  props: {},
  name: "index",
  data() {
    return {
      // is_default: [],
      checkbox: '',
      currentdate: "",
      form: {
        m_id: "",
        token: "",
        sign: "",
        province: "",
        city: "",
        area: "",
        address: "",
        is_default: '',
        scale: "",
        full_name: "",
        tel: ""
      },
      areaVisible: false,
      streetVisible: false,
      areaString: "请选择",
      streetString: "请选择",
      slots: [
        {
          flex: 1,
          values: province,
          className: "slot1",
          textAlign: "center"
        },
        {
          divider: true,
          content: "-",
          className: "slot2"
        },
        {
          flex: 1,
          values: city,
          className: "slot3",
          textAlign: "center"
        },
        {
          divider: true,
          content: "-",
          className: "slot4"
        },
        {
          flex: 1,
          values: area,
          className: "slot5",
          textAlign: "center"
        }
      ],
      slotstree: [
        {
          flex: 1,
          values: street,
          className: "slot1",
          textAlign: "center"
        }
      ]
    };
  },
  created() {
    this.getNowFormatDate();
    this.getNowFormatDate();
    let getParams = window.localStorage.getItem('params');
    let jsonObj = JSON.parse(getParams);
    let paramsData = {
      m_id: jsonObj.m_id,
      token: jsonObj.token,
      sign: "jhj",
      timestamp: this.currentdate,
      id: this.$route.query.id
    };
    this.$http.get('api_mobile/user/address/detail', { params: paramsData })
      .then(res => {
        console.log(res)
        if (res.data.status == "y") {
          this.form = res.data.result
            if( res.data.result.is_default == 1){
                this.checkbox = true;
            }
        } else {
        }
      })

  },
  methods: {
    check(){

      if(this.checkbox == true){
        this.is_default = 0;
        this.checkbox = false;
      }else{
        this.is_default = 1;
          this.checkbox = true;
      }
        console.log(this.checkbox)
    },
    handlerArea() {
      this.areaVisible = true;
    },
    onValuesChange(picker, values) {
      this.form.province = values[0];
      this.form.city = values[1];
      this.form.area = values[2];
      index = province.indexOf(values[0]);
      index2 = _m[index].indexOf(values[1]);
      if (index >= 0) {
        picker.setSlotValues(1, _m[index]);
      }
      if (index >= 0 && index2 >= 0) {
        let __d = Object.values(_c[index]);
        let area = Object.keys(__d[index2]);
        index3 = area.indexOf(values[2]);
        picker.setSlotValues(2, area);
        this.areaString = values.join(",");
        if (index3 >= 0) {
          let __a = Object.values(__d[index2]);
          let street = __a[index3];
          this.slotstree[0].values = street;
        } else {
          this.$set(this.slotstree[0].values, []);
          this.streetString = "无可选街道";
        }
      }
    },
    onStreeChange(picker, values) {
      this.streetString = values.join(",");
    },
    accomplish() {
      let getParams = window.localStorage.getItem('params');
      let jsonObj = JSON.parse(getParams);
      let paramsData = {
        m_id: jsonObj.m_id,
        token: jsonObj.token,
        sign: "jhj",
        timestamp: this.currentdate,
        full_name: this.form.full_name,
        province: this.form.province,
        city: this.form.city,
        area: this.form.area,
        address: this.form.address,
        scale: this.form.scale,
        is_default: this.is_default,
        tel: this.form.tel,
        id: this.$route.query.id
      };
      this.$http
        .get(this.$api.newAddress, {
          params: paramsData
        })
        .then(res => {
          if (res.status == 200) {
            // this.$router.push("/my/addresslist");
              if(this.$route.query.server_id){
                  this.$router.push({
                      path: "/addlist",
                      query: {
                          url: this.$route.query.url,
                          id: this.$route.query.server_id,
                      }
                  });
              }else{
                  this.$router.push("/my/addresslist");
              }
            this.form = {};
          }
        });
    },
    getNowFormatDate() {
      let date = new Date();
      let seperator = "-";
      let month = date.getMonth() + 1;
      let strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.currentdate =
        date.getFullYear() + seperator + month + seperator + strDate;
    }
  }
};
</script>
<style lang="less">
.add_site {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  .banner {
    // height: 150px;
    width: 100%;
    img {
      width: 100%;
    }
  }
  .message {
    /*height: 50%;*/
    a{padding-right:10px;}
    .mint-checkbox-label {
      margin-left: 0;
    }
    .mint-checkbox-input:checked + .mint-checkbox-core {
      background: #08d5a7;
      border-color: #08d5a7;
    }
    .mint-checklist-label {
      padding: 0;
    }
    .mint-popup-bottom {
      width: 100%;
    }
  }
  .btn {
    padding: 0 10px;
    margin-top: 8%;
    padding-bottom:100px;
    .mint-button--normal {
      color: #fff;
      width: 100%;
      background: #00c78c;
    }
  }
}
.picker-item {
  height: 36px;
  line-height: 36px;
  padding: 0 10px;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #707274;
  left: 0;
  top: 0;
  width: 100%;
  box-sizing: border-box;
}
  label .choise{
    width:18px;
    margin-right:5px;
  }
  .default-set{
    background-color:#fff;
    line-height:40px;
    padding:0 10px;
    margin-bottom:10px;
  }
  .default-set div{
    border-top:1px solid #e6e6e6;
    border-bottom:1px solid #e6e6e6;
  }
</style>



