<template>
  <div class="comment">
    <div class="personal_data clearfix">
      <div class="img">
        <img src="../../assets/tool/pic/保洁服务图片/家庭保洁.png" alt="">
      </div>
      <div class="title" :model="result">
        <p>订单号: {{result.order_no}}</p>
        <p>价格: {{ result.price_real }}</p>
        <p>服务项目: {{ result.service_name }}</p>
      </div>
    </div>
    <div class="main_t">
      <div class="discount">
          <div>
           <span>服务评价</span>
          </div>
          <div>
            <span>工作态度：</span>
            <img @click="change_star(1)" v-if='index1 >0' src="../../assets/my/pic/collect2.png"> <img @click="change_star(1)" v-else src="../../assets/my/pic/collect1.png">
            <img @click="change_star(2)" v-if='index1 >1'src="../../assets/my/pic/collect2.png">  <img @click="change_star(2)" v-else src="../../assets/my/pic/collect1.png">
            <img @click="change_star(3)" v-if='index1 >2'src="../../assets/my/pic/collect2.png">  <img @click="change_star(3)" v-else src="../../assets/my/pic/collect1.png">
            <img @click="change_star(4)" v-if='index1 >3'src="../../assets/my/pic/collect2.png">  <img @click="change_star(4)" v-else src="../../assets/my/pic/collect1.png">
            <img @click="change_star(5)" v-if='index1 >4'src="../../assets/my/pic/collect2.png">  <img @click="change_star(5)" v-else src="../../assets/my/pic/collect1.png">
          </div>
        <div>
          <span>清洁力度：</span>
          <img @click="change_star1(1)" v-if='index2 >0' src="../../assets/my/pic/collect2.png"> <img @click="change_star1(1)" v-else src="../../assets/my/pic/collect1.png">
          <img @click="change_star1(2)" v-if='index2 >1'src="../../assets/my/pic/collect2.png">  <img @click="change_star1(2)" v-else src="../../assets/my/pic/collect1.png">
          <img @click="change_star1(3)" v-if='index2 >2'src="../../assets/my/pic/collect2.png">  <img @click="change_star1(3)" v-else src="../../assets/my/pic/collect1.png">
          <img @click="change_star1(4)" v-if='index2 >3'src="../../assets/my/pic/collect2.png">  <img @click="change_star1(4)" v-else src="../../assets/my/pic/collect1.png">
          <img @click="change_star1(5)" v-if='index2 >4'src="../../assets/my/pic/collect2.png">  <img @click="change_star1(5)" v-else src="../../assets/my/pic/collect1.png">
        </div>
        <div class="note">
                <textarea placeholder="描述评价" v-model="note"></textarea>
        </div>
      </div>
    </div>
    <button class="r" size="large" @click="comment()"> 提交</button>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                currentdate: "",
                result: [],
                index1:'',
                index2:'',
                order_id:'',
                note:'',
            };
        },
        created() {
            this.order_id=this.$route.query.id;
            this.sendData();
        },
        methods:{
            change_star(index){
                console.log( index)
                this.index1=index;
            },
            change_star1(index){
                console.log( index)
                this.index2=index;
            },
            sendData(){
                let getParams = window.localStorage.getItem('params')
                let jsonObj = JSON.parse(getParams)
                let order_id = this.order_id
                let formData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '123',
                    timestamp: '123313',
                    id:order_id,
                }
                this.$http.get('api_mobile/order/order/order_detail', {params:formData})
                    .then(res => {
                        if(res.data.status == 'y'){
                            this.result = res.data.result;
                        }else{

                        }
                    })
            },
            comment (){
                let getParams = window.localStorage.getItem('params');
                let jsonObj = JSON.parse(getParams);
                if(!this.index1 || !this.index2){
                    alert('选择星级')
                    return false;
                }
                if(!this.note ){
                    alert('请填写评论')
                    return false;
                }
                /*
                if(this.note.length < 6 ){
                    alert('评论不能少于6个字')
                    return false;
                }
                */

                let formData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '123',
                    timestamp: '123313',
                    order_id:this.order_id,
                    attitude:this.index1,
                    clear:this.index2,
                    comment:this.note
                }
                this.$http.get('api_mobile/order/order/comment', {params:formData})
                    .then(res => {
                        if(res.data.status == 'y'){
                            this.result = res.data;
                            alert('评价成功')
                            this.$router.push({
                                path: '/found'
                            })
                        }else{

                        }
                    })
            }
        }
    };
</script>
<style lang="less">
  body{background: #f2f2f2;}
  .comment {
    width: 100%;
    overflow-y: auto;
    padding:0 10px;
    .personal_data {
      background: #fff;
      border-radius:5px;
      .img {
        float: left;
        img {
          padding-left: 16px;
          padding-top: 20px;
          padding-bottom:10px;
          width:80px;
        }
      }
      .main_title {
        clear:both;
        padding-top:5px;
        margin-left:10px;
        span{
          font-weight: 600;
          padding-left: 5px;
          line-height:20px;
          float:left;
          font-size:14px;
          padding-bottom:10px;
        }
        img{
          width:14px;
          height:14px;
          margin:3px;
          float:left
        }
      }
      .title {
        float:left;
        padding-top:24px;
        padding-left:10px;
        font-size: 14px;
        p{
          line-height:24px;
          font-family: '微软雅黑';
        }
      }
    }
    .main_t {
      width: 100%;
      position: relative;
      background: #fff;
      margin-top:10px;
      border-radius:5px;
      .discount {
        .note{
          padding:20px;
          height:auto;
          textarea{
            border:1px solid #b5b5b5;
            border-radius:5px;
            width:100%;
            height:100px;
            padding:10px;
          }
        }
        div {
          line-height: 40px;
          height:40px;
          border-bottom: 1px solid #e6e6e6;
          padding-left:20px;
          span {
            font-size:14px;
            line-height:40px;
            float:left;
          }
          img{
            width:20px;
            margin-top:10px;
            margin-left:15px;
          }
        }
      }
    }
    .r {
      padding: 0 25px;
      background-color: #00c78c;
      height: 38px;
      border-radius: 5px;
      width: 100%;
      color:#fff;
      margin-top:40px;
    }
  }
</style>


