<template>
  <div class="my">
    <div class="personal_data clearfix">
      <div class="img">
        <img src="../assets/my/pic/Icon2.png" alt="">
      </div>
      <div class="title">
        <p>名称: {{result.name}}</p>
        <p>卡号: {{ result.card_no }}</p>
        <p v-if="result.total_count">金额: {{ result.total_count }}</p>
        <p v-else>金额: 0</p>
      </div>
      <div class="main_title">
        <img src="../assets/my/pic/adorn.png" ></img>
        <span>品质家政服务，创造温馨舒适环境</span>
        <!-- <i class="adorn_r"></i> -->
      </div>
    </div>
    <div class="main_t">
      <div class="discount">
        <router-link to="/found">
          <div>
            <img src="../assets/found/pic/订单.png">
            <span>我的订单</span>
            <img class="img-right" src="../assets/my/pic/leftpng.png">
          </div>
        </router-link>
        <router-link to="/my/addresslist">
        <div>
          <img src="../assets/my/pic/address1.png">
          <span>服务地址</span>
          <img class="img-right" src="../assets/my/pic/leftpng.png">
        </div>
        </router-link>
        <router-link to="/my/pay">
        <div>
          <img src="../assets/my/pic/recharge1.png">
          <span>充值</span>
          <img class="img-right" src="../assets/my/pic/leftpng.png">
        </div>
        </router-link>
        <router-link to="/my/fill">
        <div>
          <img src="../assets/my/pic/account1.png">
          <span>账单</span>
          <img class="img-right" src="../assets/my/pic/leftpng.png">
        </div>
        </router-link>
        <router-link to="/my/jifen">
        <div>
          <img src="../assets/my/pic/jifen1.png">
          <span>我的积分</span>
          <img class="img-right" src="../assets/my/pic/leftpng.png">
        </div>
        </router-link>
        <router-link to="/my/set">
        <div>
          <img src="../assets/my/pic/setting1.png">
          <span>设置</span>
          <img class="img-right" src="../assets/my/pic/leftpng.png">
        </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
    import { MessageBox } from 'mint-ui';
export default {
  data() {
    return {
      currentdate: "",
      result: []
    };
  },
  created() {
    let getParams = window.localStorage.getItem('params');
    if (getParams == null) {
      this.$router.push({
        path: 'login',
      })
    } else {
      let _this =this
      let jsonObj = JSON.parse(getParams);
      let id = _this.$route.query.id;
      let localData = {
        m_id: jsonObj.m_id,
        token: jsonObj.token,
        sign: 1,
        timestamp: 3444443,
      }
      this.$http.get('api_mobile/user/info/is_reg', { params: localData })
        .then(res => {
          if (res.data.status == 'n') {
            window.localStorage.removeItem('params');
            this.$router.push({
              path: 'login',
            })
          } else if (res.data.status == 'y') {
            this.$router.push({
              path: 'my',
              query: {
                id: id
              }
            })
          }
        })
      this.getNowFormatDate();
      this.userInfo();
    }
  },
  methods: {
    getNowFormatDate() {
      let date = new Date();
      let seperator = "-";
      let month = date.getMonth() + 1;
      let strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.currentdate =
        date.getFullYear() + seperator + month + seperator + strDate;
    },
    userInfo() {
      let getParams = window.localStorage.getItem('params');
      if (getParams == null) {
        this.$router.push({
          path: 'login',
        })
      }else{
        let jsonObj = JSON.parse(getParams);
        let paramasData = {
          m_id: jsonObj.m_id,
          token: jsonObj.token,
          sign: "jhj",
          timestamp: this.currentdate
        };
        this.$http
          .get(this.$api.info, {
            params: paramasData
          })
          .then(res => {
            this.result = res.data.result;
          });
      }
    },
    address() {
      this.$router.push("/my/addresslist");
    },
    set() {
      this.$router.push("/my/set");
    },
    addFriends() {
      this.$router.push("/my/addfriends");
    },
    paylist() {
      console.log(1111)
      this.$router.push('/my/pay');
    },
    logOut() {
        MessageBox.confirm('确定退出?').then(action => {
            window.localStorage.removeItem('params');
            this.$router.push('/login');
        })
    }
  }
};
</script>
<style lang="less">
.my {
  width: 100%;
  overflow-y: auto;
  .personal_data {
    background: #f2f2f2;
    border-bottom:1px solid #e6e6e6;
    .img {
      float: left;
      img {
        margin-left: 16px;
        margin-top: 20px;
        width:80px;
        height:80px;
        border-radius:40px;
      }
    }
    .main_title {
      clear:both;
      padding-top:5px;
      margin-left:10px;
      span{
        font-weight: 600;
        padding-left: 5px;
        line-height:20px;
        float:left;
        font-size:14px;
        padding-bottom:10px;
      }
      img{
        width:14px;
        height:14px;
        margin:3px;
        float:left
      }
    }
    .title {
      float:left;
      padding-top:24px;
      padding-left:10px;
      font-size: 14px;
      p{
        line-height:24px;
        font-family: '微软雅黑';
      }
    }
  }
  .main_t {
    width: 100%;
    position: relative;
    .discount {
      div {
        line-height: 40px;
        border-bottom: 1px solid #e6e6e6;
        padding-left:20px;
        background:#fff;
        span {
          font-size:14px;
        }
        img{
          width:22px;
          margin-top:10px;
          margin-right:15px;
          float:left;
        }
        .img-right{
          height:10px;
          width:auto;
          margin-top:15px;
          margin-right:30px;
          float:right;
        }
      }
    }
  }
}
</style>


