<template>
    <div class="hello-c">
        <div class="swipe">
            <mt-swipe :auto="4000" v-model="swipedata">
                <mt-swipe-item v-for="(item, index) in swipedata" :key='index'>
                    <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
                </mt-swipe-item>
            </mt-swipe>
        </div>

        <div class="container" >
            <div v-for="(item, index) in list" :key='index' class="comment-ul">
                <div class="header-title">
                    <img :src='item.headimgurl'/>
                    <div class="comment-title">
                        <span class="title">{{item.nickname}}</span>
                        <span class="time">{{item.time}}</span>
                    </div>
                </div>
                <div class="comment11">{{item.comment}}</div>
                <ul class="clearfix ">
                    <li v-for="(item1, index1) in item.image_list" :key='index1' class="comment-ul my-img">
                        <div>
                        <a :href='"http://dev.jzfw.yaokexing.com"+item1'><img :src= '"http://dev.jzfw.yaokexing.com"+item1'    alt="">
                        </a>
                        </div>
                    </li>
                </ul>
                <div class="comment-footer">
                    <div v-if="item.is_love == 0"><img @click="addLove(item.id)" class="love" src="../assets/allcomment/wxz3.png"><span>{{item.loves}}</span></div>
                    <div v-else><img @click="addLove(item.id)" class="love" src="../assets/allcomment/xz3.png"><span class="green-color">{{item.loves}}</span></div>

                    <div v-if="item.is_talk == 0" ><img @click="addTalk(item.id,'people-comment'+item.id)" class="comment" src="../assets/allcomment/wxz2.png"><span>{{item.talks}}</span></div>
                    <div v-else ><img @click="addTalk(item.id,'people-comment'+item.id)" class="comment" src="../assets/allcomment/xz2.png"><span>{{item.talks}}</span></div>

                    <div v-if="item.is_good == 0"><img @click="addGood(item.id)" class="good" src="../assets/allcomment/wxz1.png"><span>{{item.goods}}</span></div>
                    <div v-else><img @click="addGood(item.id)" class="good" src="../assets/allcomment/xz1.png"><span class="green-color">{{item.goods}}</span></div>
                </div>
                <div class="comment-tell">
                    <div class="comment-list">
                        <div class="people" v-for="(item2, index2) in item.talk_list" :key='index2'>
                            <div><img :src='item2.headimgurl'></div>
                            <div @mouseover="showdisplay(item2.id)" @mouseout="hiddendisplay(item2.id)" @click="reBack(item2.id,item2.id,item.id,'people-comment'+item.id,item2.nickname)" >
                                <span>{{item2.nickname}} : </span>{{item2.comment}} <label>{{item2.add_time}}</label>

                                <!--
                                <span>{{item2.nickname}} : </span>{{item2.comment}} <label>{{item2.add_time}}<img v-if="imgIndex == item2.id" :class="choiseBlock" @click="reBack(item2.id,item2.id,item.id,'people-comment'+item.id,item2.nickname)" src="../assets/allcomment/wxz2.png"></label>
                            -->
                            </div>
                            <div class="people padding-left" v-for="(item3, index3) in item2.list" :key='index3' >
                                <div><img :src='item3.headimgurl'></div>
                                <div @mouseover="showdisplay('list'+item3.id)" @mouseout="hiddendisplay('list'+item3.id)" @click="reBack(item3.id,item2.id,item.id,'people-comment'+item.id,item3.nickname)" >
                                    <span>{{item3.nickname}}</span>回复<span>{{item3.nickname1}} :</span>{{item3.reback_comment}} <label>{{item3.add_time}}</label>
                                    <!--
                                    <img v-if="imgIndex == 'list'+item3.id && item3.reback_people_id == auth_id" :class="choiseBlock" @click="reBack(item3.id,item2.id,item.id,'people-comment'+item.id,item3.nickname)" src="../assets/allcomment/wxz2.png">
                                    -->

                                    <!--
                                    <span>{{item3.nickname}}</span>回复<span>{{item3.nickname1}} :</span>{{item3.reback_comment}} <label>{{item3.add_time}}<img v-if="imgIndex == 'list'+item3.id" :class="choiseBlock" @click="reBack(item3.id,item2.id,item.id,'people-comment'+item.id,item3.nickname)" src="../assets/allcomment/wxz2.png"></label>
                                    -->
                                </div>
                            </div>
                        </div>
                        <!--
                        <div class="people" @mouseover="showdisplay(2)" @mouseout="hiddendisplay(2)" >
                            <div><img src="../assets/allcomment/tp14.png"></div>
                            <div>
                                <span>地瓜瓜。  : </span>我要吃油泼面～ <label>前天 21:57</label>
                            </div>
                            <div class="people padding-left">
                                <div><img src="../assets/allcomment/tp12.png"></div>
                                <div>
                                    <span>李徐徐徐</span>回复<span>地瓜瓜。  :</span>我也是～ <label>前天 22:53<img  v-if="index == 2" :class= 'choiseBlock'  src="../assets/allcomment/wxz2.png"></label>
                                </div>
                            </div>
                        </div>
                        -->
                    </div>
                    <div style="clear:both"></div>
                    <input :class='"people-comment"+item.id' v-if="active_id == item.id" @change="changeValue" :placeholder="placeholderValue" />
                    <input :class='"people-comment"+item.id' v-else @change="changeValue" placeholder="评论" /><button @click="sendMessage(item.id,'people-comment'+item.id)">发送</button>
                    <div style="clear:both"></div>
                </div>
            </div>
        </div>

        <div class="mint-tabbar is-fixed footer-new">
            <ul>
                <li @click="redirectEdit1()">
                    <img src="../assets/allcomment/wxz4.png"/>
                    <p>首页</p>
                </li>
                <li>
                    <p @click="redirectEdit2()" class="active-edit"><img src="../assets/allcomment/xz6.png"/></p>
                </li>
                <li class="active">
                    <img src="../assets/allcomment/xz5.png"/>
                    <p>我的</p>
                </li>
            </ul>
        </div>

    </div>
</template>
<script>
import { domain } from '../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            imgIndex:0,
            choiseBlock:'displayNo',
            swipedata: [],
            cleardata: [],
            subdata: [],
            sundata: [],
            input_class: '',
            list:[],
            type:0,   // type(1-评论，2-回复),
            id:0,       //id(评论的id,0-评论,其他-回复)
            active_id:0,       //active_id动态id
            talk_id:0,          //回复的动态id
            comment:'',
            reback_comment:'',          //回复
            reback_id:0,
            auth_id:'',
            placeholderValue:'评论',
            parent_id :'',
            is_me:'yer'
        }
    },
    methods:{
        redirectEdit1(){
            this.$router.push({
                path: '/talk',
                query: {
                    openid: this.$route.query.openid
                }
            })
        },
        redirectEdit2(){
            this.$router.push({
                path: '/sendtalk',
                query: {
                    openid: this.$route.query.openid
                }
            })
        },

        showdisplay(e){
        //console.log(e)
            this.choiseBlock = '';
            this.imgIndex =e;
            this.talk_id = e;
        },
        hiddendisplay(e){
            console.log(e)
            this.choiseBlock = 'displayNo';
            this.imgIndex =e;
        },
        sendMessage(e,el){
            let formData = '';
            if(this.type > 0){
                formData = {
                    active_id:this.active_id,
                    type:this.type,
                    id:this.id,
                    comment:this.comment,
                    talk_id:this.talk_id,
                    reback_comment:this.reback_comment,
                    reback_id:this.reback_id,
                    parent_id:this.parent_id,
                }
            }else{
                formData = {
                    active_id:e,
                    type:1,
                    id:this.auth_id,
                    comment:this.comment,
                    talk_id:this.talk_id,
                    reback_comment:this.reback_comment,
                    reback_id:this.reback_id,
                }
            }

            //判断用户是否有是第一次评论

            this.$http.get('api_mobile/cat/add_comment',{params:formData})
                .then(res => {
                    console.log(res)
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        this.type=0;   // type(1-评论，2-回复),
                        this.id = 0;       //id(评论的id,0-评论,其他-回复)
                        this.active_id = 0;       //active_id动态id
                        this.talk_id = 0;          //回复的动态id
                        this.comment = '';
                        this.reback_comment = '';          //回复
                        this.reback_id = 0;
                        this.placeholderValue = '评论';
                        this.parent_id = '';
                        let element = document.querySelector("."+el);
                        element.value = '';
                        this.listActive();
                    }
                })

        },
        changeValue(e){
            let type = this.type;
            if(type == 1){
                this.comment = e.target.value;
            }else if(type ==2){
                this.reback_comment = e.target.value;
            }else{
                this.comment = e.target.value;
            }


        },
        reBack(id,parent_id,active_id,input_class,nick){
            //type(1-评论，2-回复),id(评论的id,0-评论,其他-回复)
            //获取焦点
            console.log(input_class);
            let element = document.querySelector("."+input_class);
            element.focus();
            //1.type(1-评论，2-回复)   2.id(评论的id,)   3.active_id动态id
            this.type = 2;
            this.reback_id = this.auth_id;
            this.talk_id = id;
            this.parent_id = parent_id;
            this.active_id = active_id;
            this.placeholderValue = '回复'+nick+'的评论';
        },
        addLove(active_id){
            let formData = {
                active_id:active_id,
                openid:this.auth_id
            }
            this.$http.get('api_mobile/cat/add_love',{params:formData})
                .then(res => {
                    console.log(res)
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        console.log(res);
                        this.active_id = 0;
                        this.listActive();

                    }
                })
        },
        addGood(active_id){
            let formData = {
                active_id:active_id,
                openid:this.auth_id
            }
            this.$http.get('api_mobile/cat/add_good',{params:formData})
                .then(res => {
                    console.log(res)
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        console.log(res);
                        this.active_id = 0;
                        this.listActive();
                    }
                })
        },
        listActive(){
            let openid = this.$route.query.openid;
            let formData = {
                openid: openid ? openid : '',
            }
            console.log(formData);
            this.$http.get('api_mobile/cat/my_active_list',{params:formData})
                .then(res => {
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        console.log(res);
                        this.list = res.data.result;
                        setTimeout( function() {
                                let element = document.querySelectorAll(".my-img img");
                                if (element.length > 0) {
                                    for (let i = 0; i < element.length; i++) {
                                        console.log(element[i].height);
                                        if (element[i].height < 90) {
                                            element[i].style.height = '100%';
                                        }
                                    }
                                }
                            }
                            ,200)
                    }
                })
            this.$http.post('api_mobile/goods/index/ad')
                .then(
                    res => {
                        this.swipedata = res.data.result
                    }
                )
        },
        addTalk(active_id,input_class){
            //获取焦点
            let element = document.querySelector("."+input_class);
            element.focus();
            //1.type(1-评论，2-回复)   2.id(评论的id,0-评论,其他-回复)   3.active_id动态id
            this.type = 1;
            this.id = this.auth_id;
            this.active_id = active_id;
            this.placeholderValue = '评论';
        },
    },
    created(e){
        let openid = this.$route.query.openid;
        this.listActive();
        if(!openid){
            window.location.href =  'http://dev.jzfw.yaokexing.com/api_mobile/cat/getuserinfo';
        }else{
            this.auth_id = openid;
        }
    },
    mounted(e){
        this.listActive()
        setTimeout(function(){
            let el1= document.querySelector(".swipe img");
            let el2= document.querySelector(".swipe");
            el2.style.height = el1.height+'px';
        },500)
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello-c{
    background-color: #f2f2f2;
    padding-bottom:80px;
}
.hello-c .swipe {
    width: 100%;
    height:150px;
}
.swipe .mint-swipe img{
    width:100%;
}

/* .container ul{
    height: 260px;
} */
.hello-c .container{
    background-color: #fff;
    border-radius: 10px;
}
.hello-c .container ul{
    clear:both;
    padding:5px;
}
.hello-c .container ul li {
    text-align: center;
    float: left;
    width: 33.3%;
    display: block;
    height:100px;
    overflow: hidden;
}
.hello-c .container ul li div{
    margin: 5px;
    height:90px;
    overflow: hidden;
}
.hello-c .comment11{
    padding:10px;
    clear:both;
    line-height:20px;
    font-size:12px;
}

.footer{
    margin: 5px;
    border-radius: 10px;
    margin-bottom: 50px;
}
.footer ul li {
    float: left;
    width: 50%;
    padding: 5px;
}

.hello-c .container ul li img {
    overflow: hidden;
    width: 100%;
    /*height: 100%;*/
}
.header-title img{
    height:50px;
    width:50px;
    padding:10px;
    float:left;
}
.header-title .comment-title{
   float:left;
}
.header-title .comment-title .title{
    font-size:15px;
    font-weight:bold;
    display:block;
    margin-top:16px;
    color:#2C2C2C;
}
.header-title .comment-title .time{
   font-size:12px;
}
.comment-footer .green-color{
    color:#00C88D;
}
.comment-ul{
    /*border-bottom: 1px solid #ccc;*/
    margin-bottom:10px;
}
.comment-footer .good{
    width:18px;
}
.comment-footer .comment,.comment-footer .love{
    width:20px;
}
.comment-footer div{
   display: inline-block;
    padding:10px;
}
.comment-footer span{
    font-size:13px;
    color:#D4D4D4;
    margin-left:10px;
    margin-right:20px;
}
.comment-tell input{
    margin:10px;
}
.comment-list{
      margin:10px;
    font-size:13px;
}
.comment-list span{
    color:#00C88D;
}
.comment-list label{
    display:block;
    color:#D4D4D4;
}
.comment-list label img{
    height:17px;
    width:auto;
    margin-left:10px;
}
.comment-list label img.displayNo{
   display:none;
}
.comment-list img{
    width:30px;
    height:30px;
    margin-right:10px;
    border-radius:30px;
}
.comment-list .people{
    clear:both;
}
.comment-list .people div{
    float:left;
}
.comment-list .people .padding-left{
    margin-left:40px;
}
.comment-tell input{
     border:1px solid #00C88D;
     width:70%;
    float:left;
    height:31px;
    padding: 0 5px;
    border-radius:5px;
 }
.comment-tell button{
    border:1px solid #00C88D;
    width:20%;
    float:left;
    display:block;
    margin-top:10px;
    height:31px;
    padding: 0 5px;
    background: #00C88D;
    color:#fff;
    border-radius:5px;
}
.footer-new ul{
    display:block;
    width:100%;
}
.footer-new ul li{
    display: block;
    height:55px;
    width:33.3%;
    float:left;
    text-decoration: none;
    font-size:12px;
}
.footer-new ul .active p{
    color:#00C88D;
}

.footer-new ul li img{
    margin-top:12px;
    width:20px;
}
.footer-new ul li .active-edit{
    height:42px;
    width:76px;
    background:#00C88D;
    margin:auto;
    margin-top:7px;
    border-radius:10px;
}

</style>
<style less>
    .mint-tabbar.footer.yer{
        display:none;
    }
</style>



