import Vue from 'vue'
import Router from 'vue-router'
import Main from '../page/main.vue'
import Tool from '../page/tool.vue'
import Found from '../page/found.vue'
import My from '../page/my.vue'
// import Site from '../page/my/address.vue'
import Addsite from '../page/my/addSite.vue'
import Settings from '../page/my/set.vue'
import Friends from '../page/my/addFriends.vue'
import AddressList from '../page/my/addressList.vue'
import Yu from '../page/yuyue.vue'
import YuNext from '../page/yunext.vue'
import Login from '../page/login.vue'
import Pay from '../page/my/pay.vue'
import PayNext from '../page/my/paynext.vue'
import Addlist from '../page/addlist.vue'
import orderList from '../page/found/orderlist.vue'
import Fill from '../page/my/fill.vue'
import Jifen from '../page/my/jifen.vue'
import Comment from '../page/found/comment.vue'
import MyComment from '../page/found/mycomment.vue'
import Payorder from '../page/found/payorder.vue'
import Agreement from '../page/my/agreement.vue'
import De from '../page/de.vue'
// import Introduce from '../page/my/introduce.vue'
// import Team from '../page/my/team.vue'
import New from '../page/my/new.vue'
import AllComment from '../page/comment.vue'
import YearSales from '../page/yearsales.vue'
import Actives from '../page/actives.vue'
import Introduce from '../page/introduce.vue'
import Team from '../page/team.vue'
import AddUs from '../page/addus.vue'
import EnterUs from '../page/enterus.vue'
import PerEnter from '../page/perenter.vue'
import SendTalk from '../page/sendtalk.vue'
import MyTalk from '../page/mycomment.vue'
import Client from '../page/client.vue'
import Adv from '../page/adv.vue'
import NewDetail from '../page/newdetail.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            // component: resolve => require(['@/page/main'],resolve),
            component:Main,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/main',
            component: Main,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/tool',
            component: Tool,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/found',
            component: Found,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my',
            component: My,
            meta:{
                showFooter:true
            }
        },
        // {
        //     path: '/my/site',
        //     component: Site
        // },
        {
            path: '/my/site/addsite',
            component: Addsite,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/set',
            component: Settings,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/addfriends',
            component: Friends,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/addresslist',
            component: AddressList,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/yu',
            component: Yu,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/yunext',
            component: YuNext,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/login',
            component: Login,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/pay',
            component: Pay,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/paynext',
            component: PayNext,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/addlist',
            component: Addlist,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/found/orderlist',
            component: orderList,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/fill',
            component: Fill,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/jifen',
            component: Jifen,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/found/comment',
            component: Comment,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/found/mycomment',
            component: MyComment,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/found/payorder',
            component: Payorder,
            meta:{
                showFooter:true
            }
        },
        // {
        //     path: '/my/introduce',
        //     component: Introduce
        // },
        // {
        //     path: '/my/team',
        //     component: Team
        // },
        {
            path: '/news',
            component: New,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/talk',
            component: AllComment,
            meta:{
                showFooter:false,
                showback:true
            }
        },
        {
            path: '/yearsales',
            component: YearSales,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/actives',
            component: Actives,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/introduce',
            component: Introduce,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/team',
            component: Team,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/addus',
            component: AddUs,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/enterus',
            component: EnterUs,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/perenter',
            component: PerEnter,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/sendtalk',
            component: SendTalk,
            meta:{
                showFooter:false,
                showback:true
            }
        },
        {
            path: '/mytalk',
            component: MyTalk,
            meta:{
                showFooter:false,
                showback:true
            }
        },
        {
            path: '/client',
            component: Client,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/adv',
            component: Adv,
            meta:{
                showFooter:true,
                showback:true
            }
        },
        {
            path: '/newdetail',
            component: NewDetail,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/my/agreement',
            component: Agreement,
            meta:{
                showFooter:true
            }
        },
        {
            path: '/de',
            component: De,
            meta:{
                showFooter:true
            }
        }
    ]
})


