<template>
    <div  class="big_box">
        <div class="img">
            <img v-if="id == 2" src="../assets/bannar/bannar1.png"/>
            <img v-else-if="id == 3" src="../assets/bannar/bannar8.jpg"/>
            <img v-else src="../assets/bannar/bannar9.jpg"/>
        </div>
        <div class="clearfix right_container">
            <ul class="list">

            <li v-for='(item,index) in tabContents' :key='index' @click="tabCon(item.id,item.reid)" v-if="item.id != 113" >
                <div class="li-div">
                    <div class="li-div-img">
                    <img :src='"http://dev.jzfw.yaokexing.com"+item.show_image' alt="">
                    </div>
                    <p>{{item.name}}</p>
                    <p class="sub-title">{{item.sub_desc}}</p>
                </div>
            </li>

            </ul>
        </div>
    </div>
</template>
<script>
import qs from 'qs';
    export default {
        data (){
            return{
                totalData: [],
                query:{
                    reid:0,
                    level:1
                },
                tabContents: [],
                num: '',
                id:2
            }
        },
        created(){
            console.log(789);
            this.id = this.$route.query.id;
            console.log(this.id)
            if(!this.$route.query.id){
                this.$http.post('api_mobile/goods/index/index')
                    .then(
                        res => {
                            this.tabContents = res.data.result.parent_list
                            console.log(res);
                        }

                    )
            }else{
                this.query.reid = this.$route.query.id;
                this.$http.get('api_mobile/cat/cat', { params: this.query })
                    .then(res => {
                            let _this = this

                            this.tabContents = res.data.result;
                            console.log(res);
                        }
                    )
            }

            this.num = this.$route.query.id ? this.$route.query.id - 1 : 0
        },
        methods:{
            tab(index,name) {
                this.num = index;
                if(name == '充值'){
                    this.$router.push({
                        path:'/my/pay',
                    })
                }
            },
            tabCon(index,parent_id){console.log(parent_id);
                if(parent_id > 0){
                    this.$router.push({
                        path: 'Yu',
                        query: {
                            id: index
                        }
                    })
                } else{
                    this.$router.push({
                        path: 'tool',
                        query: {
                            id: index
                        }
                    })
                }
            }

        },
        watch: {
            $route(to, from) {
                this.id = this.$route.query.id;
                console.log(this.id)
                if(!this.$route.query.id){
                    this.$http.post('api_mobile/goods/index/index')
                        .then(
                            res => {
                                this.tabContents = res.data.result.parent_list
                                console.log(res);
                            }

                        )
                }else{
                    this.query.reid = this.$route.query.id;
                    this.$http.get('api_mobile/cat/cat', { params: this.query })
                        .then(res => {
                                let _this = this

                                this.tabContents = res.data.result;
                                console.log(res);
                            }
                        )
                }
                this.num = this.$route.query.id ? this.$route.query.id - 1 : 0
            }
        }

    }
</script>

<style scoped>
    .big_box .img img{
        width:100%;
    }
.big_box {
    height: 100%;
    background: #fff;
}
.right_container {
    background: #EBEBEB;
}

.list {
    display: flex;
    flex-wrap: wrap;
}
.right_container ul{
    /*padding-right:10px;*/
    background: #EBEBEB;
}
.right_container li {
     width:50%;
     text-align:center;
 }
.right_container li .li-div .li-div-img{
    height:7.8rem;
    overflow: hidden;
}
.right_container li:nth-child(2n) {
    padding: 5px 10px 5px 5px;
}
.right_container li:nth-child(2n+1) {
    padding: 5px 5px 5px 10px;
}
.right_container li .li-div{
    background: #fff;
}
.right_container li p{
    font-size:14px;
}
.right_container li .sub-title {
    color:#999;
    font-size:10px;
    line-height:20px;
    height:40px;
    padding: 0 5px;
}

.right_container img {
    /*width: 50%;*/
    width:9.7rem;
}
.right_container ul li:last-child{
    margin-bottom: 55px;
}

</style>


