<template>
    <div class="sales">
        <div class="img">
            <img src="../assets/sales/tp21.png"/>
        </div>
        <div class="footer">
            <ul class="clearfix">
                <li v-for="(item, index) in sundata" :key='index'@click="listnum(item.id)">
                    <a href="javascript:;"><img :src='"http://dev.jzfw.yaokexing.com"+item.show_image' alt=""></a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { domain } from '../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            index:2,
            choiseBlock:'displayNo',
            swipedata: [],
            cleardata: [],
            subdata: [],
            sundata: [],
            form:{
                full_name:''
            }

        }
    },
    methods:{
        listnum(index) {
            this.$router.push({
                path: 'Yu',
                query: {
                    id: index
                }
            })
        },
        toolnum(index) {
            this.$router.push({
                path: 'tool',
                query: {
                    id: index
                }
            })
        },
        playClick(){console.log(78966)
            this.isPlay = !this.isPlay;
            this.playStatus= 'autoplay';
        },
        closeSoundClick(){console.log(789)
            this.isMute = !this.isMute;
            if(this.isMute){
                this.muteStatus = '';
            }else{
                this.muteStatus = 'muted';
            }
        },
        showdisplay(e){console.log(666);
            this.choiseBlock = '';
            this.index =e;
        },
        hiddendisplay(e){console.log(43435435);
            this.choiseBlock = 'displayNo';
            this.index =e;
        }
    },
    mounted(){
        this.$http.post('api_mobile/goods/index/ad')
          .then(
            res => {console.log(res.data.result);
                this.swipedata = res.data.result
            }
        )
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .sales,.sales .img{
        height:100%;
    }
    .sales img{
        width:100%;
        height:100%;
    }


</style>



