<template>
    <div class="allService_list">
        <div v-if="is_show">
        <ul>
            <li class="clearfix" v-for="item in orderList" :key="item.id" >
                <div class="headPortrait">
                    <img v-if="item.image" :src='"http://dev.jzfw.yaokexing.com"+item.image' alt="">
                    <img v-else src="../../assets/my/pic/微信支付图标.png" alt="">
                    <span>{{item.full_name}}</span>
                </div>
                <div class="waitServiceInfo">
                    <div><span>服务标题：</span>{{item.name}}</div>
                    <div><span>数量：</span>1</div>
                    <div><span>费用合计：</span>{{item.price_real}}</div>
                    <div><span>下单时间：</span>{{item.addtime}}</div>
                    <div style="color:#00c88d" v-if="item.status == 1 || item.status == 2"><span >交易状态：</span>等待服务</div>
                    <div style="color:#00c88d" v-else-if="item.status == 3"><span >交易状态：</span>服务中</div>
                    <div style="color:#00c88d" v-else-if="item.status == 4 || item.status == 5"><span >交易状态：</span>待评价</div>
                    <div style="color:#00c88d" v-else><span>交易状态：</span>完成</div>
                    <div><span class="order-detail order" @click="listItem(item.id)">订单</span><span @click="payorder(item.id)" v-if="item.status == 3 " class="order-detail">支付</span><span  @click="comment_order(item.id)" v-else-if="item.status == 4 || item.status == 5" class="order-detail">评价</span></div>
                </div>
            </li>
        </ul>
        </div>
        <div v-else>
            <div class="black-detail">
                <img src="../../assets/aa.gif">
                <p>咦 ~，没有数据呢 ~</p>
            </div>
        </div>
    </div>

</template>
<script>
    import axios from 'axios'
    import moment from 'moment'
    export default {
    data(){
        return{
            orderList:[],
            page:1,
            page_count:1,
            rnd:0,
            is_show:0
        }
    },
    methods:{
        getOrderList(){
            let _this = this
            let getParams = window.localStorage.getItem('params');
             if (getParams == null) {
                this.$router.push({
                    path: 'login',
                })
            }else{
                let jsonObj = JSON.parse(getParams);
                let queryData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '1',
                    timestamp: "222",
                    per_page: 1,
                    type: '',
                }
                this.$http.get(this.$api.orderList,{params: queryData})
                .then(rsp=>{
                    this.orderList = rsp.data.result.list;
                    console.log(rsp.data.result.page_count)
                    this.page_count = rsp.data.result.page_count;
                    if(rsp.data.result.list.length){
                        this.is_show = 1
                    }else{
                        this.is_show = 0
                    }
                })
            }
        },
        comment_order(id){
            this.$router.push({
                path: '/found/comment',
                query: {
                    id
                }
            })
        },
        payorder(e) {
            this.$router.push({
                path: '/found/payorder',
                query: {
                    id:e
                }
            })
        },
        See(e) {
            window.location.href = `http://dev.jzfw.yaokexing.com/api/pay/do_pay?order_no=` + e;
        },
        listItem(index){
            let getParams = window.localStorage.getItem('params');
            let jsonObj = JSON.parse(getParams);
            let listData ={
                m_id: jsonObj.m_id,
                token: jsonObj.token,
                sign: '1',
                timestamp: "22275555",
                id: index
            }
            this.$router.push({
                path: '/found/orderlist',
                query:{
                    listData
                }
            })
        },
        scroll(orderList) {
            let page = this.page;
            let pages = 20;
            window.onscroll = () => {
                // 距离底部200px时加载一次
                let bottomOfWindow = document.documentElement.offsetHeight - document.documentElement.scrollTop - window.innerHeight <= 50
                if (bottomOfWindow && page <= pages) {
                    let getParams = window.localStorage.getItem('params');
                    let jsonObj = JSON.parse(getParams);
                    page =this.page+1;
                    console.log(page);
                    let queryData = {
                        m_id: jsonObj.m_id,
                        token: jsonObj.token,
                        sign: '1',
                        timestamp: "222",
                        per_page: page,
                        type: '',
                    }
                    axios.get(this.$api.orderList, {params: queryData}).then(response => {
                        this.page_count = response.data.result.page_count;
                        for (var i in response.data.result.list) {
                            this.orderList.push(response.data.result.list[i]);
                        }
                        //orderList.push(JSON.parse(response.data.result.list))
                    })
                    this.page++;
                } else {
                    this.end = 1;
                }
            }

        }
    },
    created(){
        this.getOrderList();
        this.scroll(this.persons)
    },
    mounted() {
    }

}
</script>
<style>
    .allService_list{
        width: 100%;
        height: 100%;
        padding-bottom:70px;
    }

    .allService_list ul li{
        padding: 8px;
        margin-bottom: 5px;
        margin-top:5px;
        background: #fff;
    }

    .allService_list .headPortrait{
        width: 30%;
        float: left;
    }

    .allService_list .headPortrait img{
        width: 80px;
        height: 80px;
        border-radius: 50%;
        /* background-color: pink; */
    }

    .allService_list .headPortrait span{
        display: block;
        margin-top: 10%;
        font-size:12px;
        width:80px;
        text-align:center;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap
    }

    .allService_list .waitServiceInfo{
        width: 70%;
        float: left;
        text-align: left;
    }

     .allService_list .waitServiceInfo div,
     .allService_list .waitServiceInfo span
     {
         margin-bottom: 2%;
         font-weight: 400;
         font-size: 12px;
     }
    .waitServiceInfo .order-detail{
        display:block;
        text-align:center;
        width:60px;
        height:20px;
        line-height:20px;
        border:1px solid #e0e0e0;
        border-radius:10px;
        margin-top:10px;
        float:left;
        margin-left:10px;
    }
    .waitServiceInfo .order-detail.order{
        margin-left:60px;
    }
    .black-detail {
        padding-top: 100px;
        background: #f2f2f2;
    }
    .black-detail img{
        width:150px;
        margin: auto;
        display: block;
    }
    .black-detail p{
        margin-top:40px;
        text-align:center;
        color:#999
    }
</style>





