、协议范围")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("1、本协议由您与即刻行家政服务平台的经营者共同缔结，本协议具有合同效力。即刻行家政服务平台的经营者是指法律认可的经营即刻行家政平台网站的责任主体，即刻行家政服务网站包括但不限于即刻行家政服务，本协议项下各即刻行家政服务平台的经营者可单称或合称为“即刻行家政”。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("2、除另行明确声明外，即刻行家政服务平台服务包含任何即刻行家政服务及其关联公司提供的基于互联网以及移动网的相关服务，且均受本协议约束。 如果您不同意本协议的约定，您应立即停止注册/激活程序或停止使用即刻行家政服务平台服务。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("3、 本协议内容包括协议正文、法律声明、《即刻行家政服务规则》及所有即刻行家政已经发布或将来可能发布的各类规则、公告或通知(以下合称“即刻行家政规则”或“规则”)。所有规则为本协议不可分割的组成部分，与协议正文具有同等法律效力。")]),_vm._v(" "),_c('p',[_vm._v("三、即刻行家政服务平台服务")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("1、通过即刻行家政服务及其关联公司提供的即刻行家政服务平台服务和其它服务，会员可在即刻行家政服务平台上查询商品和服务信息、达成交易意向并进行交易、对其他会员进行评价、参加即刻行家政服务组织的活动以及使用其它信息服务及技术服务，具体以所开通的平台提供的服务内容为准。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("2、您在即刻行家政服务平台上交易过程中与其他会员发生交易纠纷时，一旦您或其它会员任一方或双方共同提交即刻行家政服务要求调处，则即刻行家政服务作为独立第三方，有权根据单方判断做出调处决定，您了解并同意接受即刻行家政服务的判断和调处决定。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("3、您了解并同意，即刻行家政服务有权应政府部门(包括司法及行政部门)的要求，向其提供您向即刻行家政服务提供的用户信息和交易记录等必要信息。如您涉嫌侵犯他人知识产权等合法权益，则即刻行家政服务亦有权在初步判断涉嫌侵权行为存在的情况下，向权利人提供您必要的身份信息。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v(" 4、您在使用即刻行家政服务平台服务过程中，所产生的应纳税赋，以及一切硬件、软件、服务及其它方面的费用，均由您独自承担。")]),_vm._v(" "),_c('p',[_vm._v("四、即刻行家政服务平台服务使用规范")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("1、在即刻行家政服务平台上使用即刻行家政服务服务过程中，您承诺遵守以下约定:")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("a)实施的所有行为均遵守国家法律、法规等规范性文件及即刻行家政服务平台各项规则的规定和要求，不违背社会公共利益或公共道德，不损害他人的合法权益，不偷逃应缴税费，不违反本协议及相关规则。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("b)不发布涉嫌侵犯他人知识产权或其它合法权益的商品或服务信息，不发布违背社会公共利益或公共道德或即刻行家政服务认为不适合在即刻行家政服务平台上的服务信息，不发布其它涉嫌违法或违反本协议及各类规则的信息。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("c)不以虚构或歪曲事实的方式不当评价其他会员，不采取不正当方式制造或提高自身的信用度，不采取不正当方式制造或提高(降低)其他会员的信用度。")]),_vm._v(" "),_c('p',{staticClass:"indent-p"},[_vm._v("d)不对即刻行家政服务平台上的任何数据作商业性利用，包括但不限于在未经人事部书面同意的情况下，以复制、传播等任何方式使用即刻行家政服务平台站上展示的资料。")]),_vm._v(" "),_c('p',{static