<template>
    <div class="fondPage">
        <!-- <header>即刻行家政</header> -->
        <div class="fondPage_header clearfix">
            <mt-navbar v-model="selected" class="clearfix">
                <mt-tab-item id="1">全部订单</mt-tab-item>
                <mt-tab-item id="2">等待服务</mt-tab-item>
                <mt-tab-item id="3">服务中</mt-tab-item>
                <mt-tab-item id="4">待评价</mt-tab-item>
                <mt-tab-item id="5">服务完成</mt-tab-item>
            </mt-navbar>
        </div>
        <div class="fondPage_body">
            <mt-tab-container v-model="selected">
                <mt-tab-container-item id="1">
                    <app-all-service></app-all-service>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                    <app-wait-service></app-wait-service>
                </mt-tab-container-item>
                <mt-tab-container-item id="3">
                    <app-servicing></app-servicing>
                </mt-tab-container-item>
                <mt-tab-container-item id="4">
                    <app-comment-service></app-comment-service>
                </mt-tab-container-item>
                <mt-tab-container-item id="5">
                    <app-accomplish></app-accomplish>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>

    </div>
</template>

<script>
import AllServiceComponent from './found/AllService.vue';
import WaitServiceComponent from './found/WaitService.vue';
import ServicingComponent from './found/Servicing.vue';
import AccomplishComponent from './found/Accomplish.vue';
import CommentServiceComponent from './found/CommentService.vue';
export default {
    components: {
        appAllService: AllServiceComponent,
        appWaitService: WaitServiceComponent,
        appServicing: ServicingComponent,
        appCommentService: CommentServiceComponent,
        appAccomplish: AccomplishComponent

    },


    data() {
        return {
            selected: '1'
        }
    },
    created(){
        let getParams = window.localStorage.getItem('params');
        if (getParams == null) {
            this.$router.push({
                path: 'login',
            })
        } else {
            let _this = this
            let jsonObj = JSON.parse(getParams);
            let id = _this.$route.query.id;
            let localData = {
                m_id: jsonObj.m_id,
                token: jsonObj.token,
                sign: 1,
                timestamp: 3444443,
            }
            this.$http.get('api_mobile/user/info/is_reg', { params: localData })
            .then(res => {
                if (res.data.status == 'n') {
                    window.localStorage.removeItem('params');
                    this.$router.push({
                        path: 'login',
                    })
                } else if (res.data.status == 'y') {
                    this.$router.push({
                        path: 'found',
                        query: {
                            id: id
                        }
                    })
                }
            })
        }
    }

}
</script>

<style scoped>
header {
    height: 8%;
    background-color: #00c88c;
    color: #ffffff;
    font-size: 20px;
    font-weight: bold;
    padding: 2% 0%;
}

.fondPage {
    height: 92%;
}

/* .fondPage_header {
    height: 48px;
    border-top: 1px solid #8a8a8a;
    border-bottom: 1px solid #8a8a8a;
    margin-bottom: 20px;
} */

.fondPage_header .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 0px;
    color: #01c88d;
}

.fondPage_body {
    height: 80%;
    background: #f2f2f2;
}
.black-detail {
    padding-top: 100px;
    background: #f2f2f2;
}
.black-detail img{
    width:150px;
    margin: auto;
    display: block;
}
.black-detail p{
    margin-top:40px;
    text-align:center;
    color:#999
}
</style>


