<template>
    <div class="actives">
        <img v-if="image" :src='"http://dev.jzfw.yaokexing.com"+image'/>
        <img v-else src="../assets/active1.jpg"/>
    </div>
</template>
<script>
import { domain } from '../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            index:2,
            choiseBlock:'displayNo',
            swipedata: [],
            cleardata: [],
            subdata: [],
            sundata: [],
            image:'',
            form:{
                full_name:''
            }

        }
    },
    mounted (){
        this.$http
            .get(this.$api.activesImg)
            .then(res => {
                if (res.data.result) {
                    this.image = res.data.result.images;

                } else {
                    this.image = '';
                }
                console.log(this.image)
            })
            .catch(err => {
                console.log("getAddressList有异常", err);
            });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .actives{
        padding-bottom:55px;
    }
    .actives img{
        width:100%;
    }


</style>



