<template>
    <div class="yu-next">
        <div class="top-content" :model="topData">
            <div class="left-content">
                <img :src="'http://dev.jzfw.yaokexing.com' +topData.show_image" alt="">
            </div>
            <div class="right-content">
                <ul>
                    <li>
                        <p>{{topData.name}}</p>
                    </li>
                    <li>
                        <p>{{topData.amount}}/{{topData.type_name}}</p>
                    </li>
                </ul>
            </div>
        </div>
        <div class="cen-content" :model="cenData">
            <p>卡号:&nbsp;&nbsp;{{cenData.card_no}}</p>
            <p>金额:&nbsp;&nbsp;{{cenData.total_count}}</p>
            <!--<p>保养折扣:{{cenData.care_discount}}</p>-->
            <!--<p>钟点工折扣:{{cenData.people_discount}}</p>-->
            <!--<p>折扣优惠期:{{cenData.endtime}}</p>-->
        </div>
        <div class="foot-content">
            <div @click="addresslist()" :model="addData" style="margin:0 8px"><div>
                <span class="address-title">服务地址：</span>
                <div class="address-detail">
                    <p><span class="address-left">{{addData.full_name}}</span><span class="address-right">{{addData.tel}}</span></p>
                    <p class="address-list">{{addData.province}} {{addData.city}}{{addData.area}}{{addData.address}}</p>
                </div>

                <!--<img src="../assets/my/pic/snipaste_20180611_231911.png" style="width: 15px; float: right;" alt="">-->
                <i class="left-arrow"></i>
                <div class="clear"></div>
            </div>
            </div>
            <div class="people_list"><span>预约人数:</span>
                <div class="pepole_num">
                    <span class="add" @click="add">+</span>
                        <input class="text-boex" type="text" v-model="counter" style="width:30px;text-align:center;float:left;height:20px;line-height:20px;font-size:16px;">
                    <span class="min" @click="min" :style="minClass">-</span>
                </div>
            </div>
            <div class="date_select clearfix datetime-second">
                预约时间：
                <input type="text" v-model="date1" @click="open('picker1')" style="font-size:16px;">
                <!-- <mt-field label="预约时间" placeholder="请选择日期" type="date" v-model="birthday"></mt-field> -->
            <!--<button @click="open('picker1')" class="found_but">设置</button>-->
                <i class="left-arrow" @click="open('picker1')"></i>
            <mt-datetime-picker ref="picker1" type="datetime"  class="88908-"
                v-model="birthday" 
                year-format="{value} 年" 
                month-format="{value} 月" 
                date-format="{value} 日"
                hourFormat="{value}时"
                minuteFormat="{value}分"
                :startDate="startDate" 
                @confirm="handleChange">
            </mt-datetime-picker>
            </div>
            <div class="text_content"><span>请输入更多需求</span></div>
            <div :model="textrea" class="text-require">
                <textarea style="width:100%;">
                </textarea>
            </div>
        </div> 
    <mt-button type="primary" class="yue_mt" size="large" @click="sendData()">立即预约</mt-button>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                topData: '',
                cenData: '',
                counter: 1,
                birthday: new Date(),
                addData: {
                    province: '',
                    city: '',
                    area: '',
                    full_name: ''
                },
                textrea: '',
                value: null,
                date1: '',
                minClass:'color:#999;border:none;background:#ddd',
                //show: true,
                startDate: new Date(),
            }
        },
        methods:{
            open(picker) {
                this.$refs[picker].open();
            },
            handleChange(value) {
                let date = new Date(value)
                let year = date.getFullYear();//年
                let month = date.getMonth() + 1;//月份（月份是从0~11，所以显示时要加1）
                let day = date.getDate();//日期
                let hour = date.getHours();//日期
                let second = date.getMinutes();//日期
                if(month < 10){
                    month = '0'+ month;
                }
                if(day < 10){
                    day = '0'+ day;
                }
                if(hour < 10){
                    hour = '0'+ hour;
                }
                if(second < 10){
                    second = '0'+ second;
                }
                this.date1 = year + '-' + month + '-' + day + ' ' + hour + ':' + second;
            },
            openPicker() {
                this.$refs.picker.open();
            },
            addresslist(){
                console.log(new Date())
                this.$router.push({
                    path: 'addlist',
                    query: {
                        id: this.$route.query.id,
                        url:'/yunext?id='+this.$route.query.id+'&start_time='+this.birthday+'&num_pepole='+this.counter+'&note='+this.textrea
                    }
                })
            },
            min(){
                if(this.counter>1 && this.counter == 2){
                    this.counter = parseInt(this.counter) - 1;
                    this.minClass = 'color:#999;border:none;background:#ddd';
                }else if(this.counter>1){
                    this.counter = parseInt(this.counter) - 1;
                    this.minClass = '';

                }else{
                    this.minClass = 'color:#999;border:none;background:#ddd';
                }
            },
            add(){
                this.counter = parseInt(this.counter) + 1;
                this.minClass = '';
            },
            sendData(){
                let getParams = window.localStorage.getItem('params');
                let jsonObj = JSON.parse(getParams);
                let service_id = this.$route.query.id;
                let address_id = this.$route.query.adress_id;
                let formData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '123',
                    timestamp: '123313',
                    service_id: service_id,
                    address_id: address_id,
                    start_time: this.birthday,
                    num_pepole: this.counter,
                    note: this.textrea

                }
                this.$http.get('api_mobile/order/order/create_order', {params:formData})
                .then(res => {
                    if(res.data.status == 'y'){
                        this.$router.push({
                            path: 'found'
                        })
                    }else{
                        alert(res.data.info)
                    }
                }) 
            }
        },
        created() {
            let getParams = window.localStorage.getItem('params');
            let jsonObj = JSON.parse(getParams);
            let id = this.$route.query.id
            this.$http.get('api_mobile/cat/info?id=' + id)
            .then(res => {
                if (res.data.status == 'y') {console.log(res)
                    this.topData = res.data.result
                }
            })
            let formData = {
                m_id: jsonObj.m_id,
                token: jsonObj.token,
                sign: 1,
                timestamp: 909909
            }
            this.$http.get('api_mobile/user/info/user_info',{params: formData})
            .then(res => {
                this.cenData = res.data.result
                this.cenData.endtime = res.data.result.endtime
            })
            let addressData ={
                m_id: jsonObj.m_id,
                token: jsonObj.token,
                sign: 1,
                timestamp: 122331,
                id: this.$route.query.adress_id
            }
            this.$http.get('api_mobile/user/address/detail',{params: addressData})
            .then(res => {
                if(res.data.status == 'y'){
                    this.addData = res.data.result
                    console.log(res.data.result)
                }else{
                    console.log(res.data.info)
                }
            })
            console.log(this.$route.query.url)


        }

    }
</script>
<style>
    .datetime-second .mint-datetime .picker-item{
        font-size:14px;
    }
</style>
<style scoped>

.found_but{
  float: right;
    width:60px;
    height:24px;
    line-height:24px;
    margin-top:8px;
    background-color: #07d5a6;
    border-radius: 5px;
    color: #ffffff;
}
.foot-content .address-title{
    float:left;
    font-size:14px;
}
.foot-content .address-detail{
    float:left;
    width:65%;
}
.foot-content .address-detail .address-list{
    line-height:30px;
    height:auto;
    margin-bottom:10px;
}
.foot-content .address-detail p{
    height:25px;
    line-height:25px;
}
.address-left{
     float:left;
 }
.address-right{
    float:right;
}
.left-arrow{
    float:right;
    display: inline-block;
    width: 10px;
    height: 10px;
    vertical-align: -2px;
    border-top: 2px solid #ddd;
    border-right: 2px solid #ddd;
    -webkit-transform: rotate(45deg);
    margin-top:15px;
}

.text_content{
    height:40px;
    line-height:40px;
    margin:0 8px;
    font-size:14px;
}
.text-require{
    margin:0 8px;
    padding: 15px;
    border:1px solid #ddd;
    border-radius:5px;
    margin-bottom:40px;
}

.date_select{
    /*border-bottom: 1px solid #aeaeae;*/
    height:40px;
    line-height:40px;
    margin:0 8px;
    font-size:14px;
}
.people_list span{
    float:left;
}
.people_list{  
    /*border-bottom: 1px solid #aeaeae;*/
    height:40px;
    line-height:40px;
    margin:0 8px;
    font-size:14px;
}
.pepole_num{
    float: right;
    border-radius: 5px;
    margin-top:10px;
}
.pepole_num span{
    text-align:center;
    /*background-color: #b5b5b5;*/
    float:left;
    width:30px;
    border-radius:5px;
    color:#0cd17b;
    height:20px;
    line-height:18px;
    border:1px solid #0cd17b;
}
.yu-next{
    height: 100%;
    padding: 10px;
    background-color: #f2f2f2;
}    
.yue_mt{
    position: fixed;
    bottom: 56px;
    margin: 0 auto;          
}
.top-content{
    height: 25%;
    background-color: #fff;
    border-radius: 5px;
}
.left-content{
    width: 40%;
    float: left;
}
.left-content img{
    width: 80%;
    padding: 10%;
}                                                                    
.right-content{
    padding-top: 10px;
}
.cen-content{
    background-color: #fff;
    border-radius: 5px;
    margin: 8px 0;
    padding:8px 0;
}
.cen-content p{
    /*border-bottom: 1px solid #aeaeae;*/
    padding: 0 8px;

}
.foot-content{
    background-color: #fff;
    border-radius: 5px;
    padding: 8px 8px 70px 0;
}
.foot-content p{
    /*border-bottom: 1px solid #aeaeae;*/
    padding: 0 8px;

    
}
.clear{clear:both}
.picker-slot.picker-slot-center{
    width:33%;
}
.yu-next p{
    height:40px;
    line-height:40px;
    font-size:14px;


}
</style>


