<template>
    <div class="content">
        <h3>— 即刻行家政欢迎您 —</h3>
        <div class="login_content" ref="rulesfrom" :model="rulesfrom" :rules = "rules">
            <input  placeholder="请输入手机号"  v-model="rulesfrom.username" class="phone-input"></input>
            <!--
            <input  placeholder="请输入图形验证码" v-model="rulesfrom.captcha" class="captcha-input"></input>

            <div class="pic-text">
                <ul class="clearfix">
                    <li style="width:60%; border: 1px solid #dddddd">
                        <img :src="captcha" alt="">
                    </li>
                    <li style="width:40%; padding:5px;" @click="updateRefresh()">
                        <p>换一张</p>
                    </li>
                </ul>
            </div>
            -->
            <input  placeholder="请输入手机验证码"  v-model="rulesfrom.code" class="num-input"></input>
            <!-- <mt-button type="primary" class="l"  >获取验证码</mt-button> -->
            <button class="l" @click="send">
                <span v-if="sendMsgDisabled">{{time+'秒后重发'}}</span>
                <span v-if="!sendMsgDisabled">获取验证码</span>
            </button>
        </div>
        <mt-button type="primary" class="login_mt" size="large" @click="logsuccess()">立即登录</mt-button>
        <h4>下次再登录</h4>
    </div>
</template>

<script>
    export default {
        data(){
            return{ 
                time: 60,//发送验证码的倒计时
                sendMsgDisabled: false,
                captcha: 'http://dev.jzfw.yaokexing.com/' + 'api_mobile/user/reg/verify_code',
                rulesfrom:{
                    username: '',
                    captcha: '',
                    code: ''
                },
                rules: {
                    username: [
                        { type: 'number', required: true, message:'请输入正确的手机号', trigger: 'blur' }
                    ],
                    /*
                    captcha: [
                        { required: true, message: '请输入图形验证码', trigger: 'change' }
                    ],
                    */
                    code: [
                        { required: true, message: '请输入动态验证码', trigger: 'change' }
                    ],
                },
            }
        },
        methods:{
            send(){
                let forData = {
                    mobile: this.rulesfrom.username,
                    code_img: this.rulesfrom.captcha,
                    str: '64trgtrge'
                }
                this.$http.get('api_mobile/user/reg/send', {
                    params: forData
                    })
                    .then(res => {
                        if(res.data.status == 'y'){
                            this.sendMsgDisabled = true;
                            let interval = window.setInterval(() => {
                                if ((this.time--) <= 0) {
                                    this.time = 60;
                                    this.sendMsgDisabled = false;
                                    window.clearInterval(interval);
                                }
                            }, 1000)
                        }else {
                            // let _this = this
                            console.log(res.data.info)
                            alert(res.data.info)
                           
                        }
                    })
            },
            updateRefresh(){
                this.captcha = `http://dev.jzfw.yaokexing.com/api_mobile/user/reg/verify_code?=${Math.random()}`;
            },
            logsuccess(){
                let formData = {
                    username: this.rulesfrom.username,
                    code: this.rulesfrom.code
                }
                this.$http.get('api_mobile/user/reg/user_reg',{ params:formData})
                .then(res => {
                    if (res.data.status == 'n') {
                        alert(res.data.info)
                    }else if(res.data.status == 'y'){
                        let params ={
                            expire: res.data.result.expire,
                            m_id: res.data.result.m_id,
                            salt: res.data.result.salt,
                            token: res.data.result.token,
                        }
                        let str_params = JSON.stringify(params)
                        window.localStorage.setItem('params', str_params);
                        this.$router.back()
                    }
                })
            }
        },
        created(){
           this.updateRefresh();
        },

    }
</script>

<style scoped>
.content{
    padding: 20px;
}

h3{
    text-align: center;
    color: #08d5a7;
    font-weight: 600;
}

.login_content{
    margin-bottom: 10%;
}

.change{
    margin-top: 5%;
}

input{
    border-radius: 5px;
    border: 1px solid #a1a1a1;
    padding-left: 5px;
    margin-top: 5%;
}
.phone-input{
    width: 100%;
    height: 40px;
}
.captcha-input{
    width: 45%;
    height: 40px;
    position: relative;
}
.num-input{
    width: 45%;
    height: 40px;
}
.l{
    padding: 0 14%;
    margin-top: 5%;
    float: right;
    background-color: #08d5a7;
    height: 40px;
}
.l span{
   color:#fff;
}

.login_mt{
    background-color: #08d5a7;
}
h4{
    margin-top:55%;
    font-size: 14px;
    text-align: center;
    color: #dddddd
}

.pic-text{
    margin-top: 5%;
    float: right;
    width: 50%;
    height: 40px;
}
.pic-text li img{
    width:100%;
    height: 100%;
}

ul li{
    float: left;
    height: 40px;
}
ul li p{
    padding-top: 5px;
    color: #08d5a7;
    font-size:12px
}


</style>


