<template>
  <div class="comment">
    <div class="personal_data clearfix">
      <div class="img">
        <img src="../../assets/tool/pic/保洁服务图片/家庭保洁.png" alt="">
      </div>
      <div class="title" :model="result">
        <p>订单号: {{result.order_no}}</p>
        <p>价格: {{ result.price_real }}</p>
        <p>服务项目: {{ result.service_name }}</p>
      </div>
    </div>
    <div class="main_t">
      <div class="discount">
          <div>
           <span>服务评价</span>
          </div>
          <div>
            <span>工作态度：</span>
            <img v-if='comment.attitude >0' src="../../assets/my/pic/collect2.png"> <img v-else src="../../assets/my/pic/collect1.png">
            <img v-if='comment.attitude >1'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
            <img v-if='comment.attitude >2'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
            <img v-if='comment.attitude >3'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
            <img v-if='comment.attitude >4'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
          </div>
        <div>
          <span>清洁力度：</span>
          <img v-if='comment.clear >0' src="../../assets/my/pic/collect2.png"> <img v-else src="../../assets/my/pic/collect1.png">
          <img v-if='comment.clear >1'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
          <img v-if='comment.clear >2'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
          <img v-if='comment.clear >3'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
          <img v-if='comment.clear >4'src="../../assets/my/pic/collect2.png">  <img v-else src="../../assets/my/pic/collect1.png">
        </div>
        <div class="note">
                <p>评论：{{comment.comment}}</p>
                <p>回复：{{comment.call_back}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                currentdate: "",
                result: [],
                comment:[],
                order_id:'',
                note:'',
            };
        },
        created() {
            this.order_id=this.$route.query.id;
            this.sendData();
        },
        methods:{
            sendData(){
                let getParams = window.localStorage.getItem('params')
                let jsonObj = JSON.parse(getParams)
                let order_id = this.order_id
                let formData = {
                    m_id: jsonObj.m_id,
                    token: jsonObj.token,
                    sign: '123',
                    timestamp: '123313',
                    id:order_id,
                }
                this.$http.get('api_mobile/order/order/order_detail', {params:formData})
                    .then(res => {
                        if(res.data.status == 'y'){
                            this.result = res.data.result;
                        }else{

                        }
                    })
                this.$http.get('api_mobile/order/order/comment_list', {params:formData})
                    .then(res => {
                        if(res.data.status == 'y'){
                            this.comment = res.data.result;
                            console.log(this.comment);
                        }else{

                        }
                    })
            },
        }
    };
</script>
<style lang="less">
  body{background: #f2f2f2;}
  .comment {
    width: 100%;
    overflow-y: auto;
    padding:0 10px;
    .personal_data {
      background: #fff;
      border-radius:5px;
      .img {
        float: left;
        img {
          padding-left: 16px;
          padding-top: 20px;
          padding-bottom:10px;
          width:80px;
        }
      }
      .main_title {
        clear:both;
        padding-top:5px;
        margin-left:10px;
        span{
          font-weight: 600;
          padding-left: 5px;
          line-height:20px;
          float:left;
          font-size:14px;
          padding-bottom:10px;
        }
        img{
          width:14px;
          height:14px;
          margin:3px;
          float:left
        }
      }
      .title {
        float:left;
        padding-top:24px;
        padding-left:10px;
        font-size: 14px;
        p{
          line-height:24px;
          font-family: '微软雅黑';
        }
      }
    }
    .main_t {
      width: 100%;
      position: relative;
      background: #fff;
      margin-top:10px;
      border-radius:5px;
      .discount {
        .note{
          padding:20px;
          height:auto;
          textarea{
            border:1px solid #b5b5b5;
            border-radius:5px;
            width:100%;
            height:100px;
            padding:10px;
          }
        }
        div {
          line-height: 40px;
          height:40px;
          border-bottom: 1px solid #e6e6e6;
          padding-left:20px;
          span {
            font-size:14px;
            line-height:40px;
            float:left;
          }
          img{
            width:20px;
            margin-top:10px;
            margin-left:15px;
          }
        }
      }
    }
    .r {
      padding: 0 25px;
      background-color: #00c78c;
      height: 38px;
      border-radius: 5px;
      width: 100%;
      color:#fff;
      margin-top:40px;
    }
  }
</style>


