export default function (Vue) {
    // 年月
    Vue.filter('dateDate', function (tplData) {
        let dateMM = tplData ? new Date(tplData) : new Date();
        return `${dateMM.getFullYear()}-${dateMM.getMonth() + 1}-${dateMM.getDate()}`;
    });
};



