                            <</video>

            <!--
                                   <video width="320" height="240" controls>
                                       <source src="http://dev.jzfw.yaokexing.com/public/images/jkx.mp4" type="video/mp4">

                                   </video>
                                   -->
            <span class="ico ico-sound" :class="{ active: isMute }" v-on:click="closeSoundClick()"></span>
            <span class="ico ico-skip"></span>
            <span class="ico ico-video" :class="{ hide: isPlay }" v-on:click="playClick()"></span>

        </div>
        <div class="div1">
            <div class="div2">
                <p>尊敬的客户：您好！因微信公众平台正在升级调整，如您在使用过程中出现平台功能导常，敬请联系客服，谢谢!</p>
            </div>
        </div>

        <div class="container">
            <ul class="clearfix ">
                <li  v-for="(item, index) in cleardata" :key='index' @click="toolnum(item.id,item.reid)">
                    <a href="javascript:;"><img :src='"http://dev.jzfw.yaokexing.com"+item.image' alt="">
                    <p>{{item.name}}</p>
                    </a>
                </li>
            </ul>
        </div>
        <div class="active-news">
            <div class="footer-title"><label></label><div>活动预告</div></div>
        </div>
        <!--
        <div class="swipe-wrapper" style="height:70px;margin:0 5px;background: #fff;padding:10px 0;border-radius:5px;">
            <mt-swipe :auto="1000" v-model="swipedata"  style="width:33%;float:left;padding:0 5px;" animating="false">
                <mt-swipe-item v-for="(item, index) in swipedata" :key='index'>
                    <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
                </mt-swipe-item>
            </mt-swipe>
            <mt-swipe :auto="1000" v-model="swipedata2"  style="width:33%;float:left;padding:0 5px;">
                <mt-swipe-item v-for="(item, index) in swipedata2" :key='index' >
                    <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
                </mt-swipe-item>
            </mt-swipe>
            <mt-swipe :auto="1000" v-model="swipedata3"  style="width:33%;float:left;padding:0 5px;">
                <mt-swipe-item v-for="(item, index) in swipedata3" :key='index' >
                    <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
                </mt-swipe-item>
            </mt-swipe>
        </div>
        -->
        <el-carousel :interval="2000" type="card" height="100px" indicator-position="none" class="swipr-card-list">
            <el-carousel-item v-for="(item, index) in swipedata" :key='index'>
                <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
            </el-carousel-item>
        </el-carousel>
        <div class="footer">
            <div class="footer-title"><label></label><div>特色服务</div></div>
            <ul class="clearfix">
                <li v-for="(item, index) in subdata" :key='index'@click="listnum(item.id)" :class="'className'+index">
                    <div class="li-div">
                        <div class="li-div-desc">
                            <p class="title">{{item.name}}</p>
                            <!--<p class="sub-desc">{{item.sub_desc}}</p>-->
                            <p class="sub-desc" v-for="(item1, index1) in item.title_list" :key='index1'>{{item1}}</p>
                        </div>
                        <div class="desc-img">
                            <img :src='"http://dev.jzfw.yaokexing.com"+item.show_image' alt="">
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class="company-ad">
            <p class="contact">即刻行家政热线：023-67383851</p>
            <p class="work-time">早：8:00-晚：23:00 &nbsp;&nbsp;&nbsp;&nbsp;全年无休</p>
            <p class="word">品质到家 乐享生活</p>
        </div>
    </div>
</template>
<script>
import { domain } from '../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            swipedata: [],
            swipedata2: [],
            swipedata3: [],
            cleardata: [],
            subdata: [],
            sundata: [],
            // circledata: [],
            // totaldata:[],
            _dom:"",
            //videoSrc:'http://babylife.qiniudn.com/FtRVyPQHHocjVYjeJSrcwDkApTLQ',
            // videoImg:'http://static.fdc.com.cn/avatar/usercenter/5996999fa093c04d4b4dbaf1_162.jpg',
            videoSrc:'http://dev.jzfw.yaokexing.com/public/images/jkx.mp4',
            videoImg:'http://dev.jzfw.yaokexing.com/public/images/jkx.jpg',
            playStatus:'',
            muteStatus:'',
            isMute:true,
            isPlay:true,
            className0:'red',
            className1:'red',
            className2:'red',
            className3:'red',
//        controlBar: {
//          timeDivider: true,
//          durationDisplay: true,
//          remainingTimeDisplay: false,
//          fullscreenToggle: true  //全屏按钮
//        }
        }
    },
    methods:{
        listnum(index) {
            this.$router.push({
                path: 'Yu',
                query: {
                    id: index
                }
            })
        },
        toolnum(index,parent_id) {
            if(parent_id > 0){
                this.$router.push({
                    path: 'Yu',
                    query: {
                        id: index
                    }
                })
            }else if(index == 113){
                this.$router.push({
                    path: 'my/pay',
                })
            }else{
                this.$router.push({
                    path: 'tool',
                    query: {
                        id: index
                    }
                })
            }

        },
        playClick(){
            this.isPlay = !this.isPlay;
            this.playStatus= 'autoplay';
        },
        closeSoundClick(){
            this.isMute = !this.isMute;
            if(this.isMute){
                this.muteStatus = '';
            }else{
                this.muteStatus = 'muted';
            }
        }
    },
    mounted(){

        this.$http.post('api_mobile/goods/index/index')
          .then(
            res => {
                console.log(res)
                let _this = this
                _this.cleardata = res.data.result.parent_list
                _this.subdata = res.data.result.sub_list1
                _this.sundata = res.data.result.sub_list2
                // var totaldata = _this.cleardata.concat(_this.subdata);
                // this.circledata = totaldata
            }
        )
        this.$http.post('api_mobile/goods/index/ad')
          .then(
            res => {
                this.swipedata = res.data.result
                this.swipedata2[0] = res.data.result[1];
                this.swipedata2[1] = res.data.result[2];
                this.swipedata2[2] = res.data.result[0];
                this.swipedata3[0] = res.data.result[2];
                this.swipedata3[1] = res.data.result[0];
                this.swipedata3[2] = res.data.result[1];
            }
        )
        /*
        setTimeout( function() {
                let element = document.querySelectorAll(".footer .li-div .desc-img");
                let element1 = document.querySelectorAll(".footer .li-div img");
                if (element.length > 0) {
                    for (let i = 0; i < element.length; i++) {
                        // if (element[i].height > element[i].width) {
                        console.log(element1[1].width);
                            element[i].style.height = element1[1].width+'px';
                        // }
                    }
                }
            }
            ,500)
            */
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .mint-swipe-indicator{
        display:none;
    }
</style>
<style scoped>
    /**轮播文字样式*/
    .swipr-card-list{
        background: #fff;
        margin:0 5px;
        padding:5px 0;
    }
    .el-carousel__item img{
       width:100%;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
    .div1 {
        margin:auto;
        position:relative;
        /*width:1000px;*/
    }
    @keyframes anis {
        100% {
            transform:translateX(-950px)
        }
    }
    .div2 {
        animation:anis 15s linear infinite;
    }
    .div2 p {
        white-space:nowrap;
        margin-left:95%;
        color:red;
        font-size:12px;
    }
    .div2:hover {
        animation-play-state:paused;
    }



.hello{
    background-color: #f2f2f2;
}
.swipe {
    width: 100%;
    /*height:150px;*/
}

/* .container ul{
    height: 260px;
} */
.container{
    background-color: #ffffff;
    margin: 5px 10px;
    /*border-radius: 10px;*/
}
.container ul li {
    text-align: center;
    float: left;
    width: 33.3%;
    display: block;
    padding: 15px;
}
.container ul li img {
    /*width:50%;*/
    width: auto;
    height: 3rem;
}
.container ul li p{
    font-size:12px;
    margin-top:5px;
}


.footer{
     margin: 5px;
     border-radius: 10px;
 }
.footer ul li {
    float: left;
    width: 50%;
}
.footer ul li label{
   display:block;
    float:left;
    width:2px;
    height:20px;
    /*background: rgb(22,222,224);*/
    margin-right:10px;
}
.footer ul li p{
    color:#fff;
    /*background: rgba(58,50,60,.5);*/
    font-size:14px;
    text-align:center;
    /*white-space: nowrap;*/
}
.footer ul li .li-div{
    position:relative;
    margin:5px;
    background: #fff;
    padding-bottom:20px;
}
.footer ul li .li-div .li-div-desc .title{
    color:#2C2C2C;
    font-weight:bold;
    font-size:16px;
    padding-top:20px;
}
.footer ul li .li-div .li-div-desc .sub-desc{
    /*color:#494949;*/
    color:#999;
    font-size:12px;
    line-height:16px;
    height:16px;
    padding:0 20px;
    text-align:center;
    /*margin-bottom:10px;*/
    /*text-overflow: ellipsis;*/
    /*white-space: nowrap;*/
    /*overflow: hidden;*/
}
.footer ul li .li-div img{
    display:block;
}
.footer ul .className0 label{
    background: rgb(239,67,66);
}
.footer ul .className1 label{
    background: rgb(22,222,224);
}
.footer ul .className2 label{
    background: rgb(239,67,229);
}
.footer ul .className3 label{
    background: rgb(241,121,59);
}
.footer ul li .li-div img {
    /*border-radius: 50%;*/
    /*overflow: hidden;*/
    /*width: 50%;*/
    /*height: 50%;*/
    /*margin:auto;*/
    /*height:auto;*/
}
.footer ul li .li-div .desc-img {
    border-radius: 50%;
    /*overflow: hidden;*/
    /*width: 50%;*/
    width:5.5rem;
    height:5.5rem;
    overflow: hidden;
    margin:auto;
}
.footer .footer-title,.active-news .footer-title{
    margin-left:10px;
    height:30px;
    line-height:30px;
}
.footer .footer-title label,.active-news .footer-title label{
    height:18px;
    width:2.5px;
    background: #00C88D;
    float:left;
    margin-right:10px;
    line-height:30px;
    margin-top:6px;
}
.footer .footer-title div,.active-news .footer-title div{
    height:30px;
    line-height:30px;
    color:#00C88D;
    font-size:17px;
    font-weight:bold;
}
img{
    width: 100%;
    height: 100%;
}
.alert-page{
    position:absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.gray-alert{
    position:absolute;
    top:0;
    border-radius:10px;
    width:100%;
    height:100%;
    background: rgba(9,9,9,0.5);
}
.company-ad{
    margin: 5px 10px;
    margin-bottom: 55px;
   background:#fff;
    text-align:center;
}
.company-ad p{
    color:#999;
    padding-top:5px;
}
.company-ad .contact{
    font-size:15px;
    padding-top:5px;
}
.company-ad .work-time{
    font-size:12px;
}
.company-ad .word{
    font-size:10px;
    padding-bottom:10px;
    border-bottom: 10px solid #f2f2f2;
}
</style>



