<template>
    <div class="new">
        <!--
        <div class="swipe">
            <mt-swipe :auto="4000" v-model="swipedata">
                <mt-swipe-item v-for="(item, index) in swipedata" :key='index'>
                    <img :src= '"http://dev.jzfw.yaokexing.com"+item.desc'    alt="">
                </mt-swipe-item>
            </mt-swipe>

        </div>
        -->
        <div class="img">
            <img src="../../assets/bannar/bannar2.png"/>
        </div>

        <div class="container">
          <ul>
              <li v-for="(item,index) in list" :key="index">
                  <a :href='"#/newdetail?id="+item.id'>
                      <div>
                          <span><img style="height:30px;width:30px;" :src='"http://dev.jzfw.yaokexing.com"+item.image'></span>
                          <!-- <img src="http://img.weimob.com/static/c1/7c/df/image/20171104/20171104105556_65659.jpg" /> </span>--></div>
                      <div>
                          <p><strong>{{item.name}}</strong></p>
                      </div>
                      <div><i class="ico_arrow"></i></div>
                  </a>
              </li>
          </ul>
        </div>
    </div>
</template>
<script>
import { domain } from '../../api/api_config'
export default {
    name: 'HelloWorld',
    data() {
        return {
            swipedata: [],
            list: [],
            subdata: [],
            sundata: [],
        }
    },
    methods:{
        listnum(index) {
            this.$router.push({
                path: 'Yu',
                query: {
                    id: index
                }
            })
        },
        toolnum(index) {
            this.$router.push({
                path: 'tool',
                query: {
                    id: index
                }
            })
        },
        playClick(){console.log(78966)
            this.isPlay = !this.isPlay;
            this.playStatus= 'autoplay';
        },
        closeSoundClick(){console.log(789)
            this.isMute = !this.isMute;
            if(this.isMute){
                this.muteStatus = '';
            }else{
                this.muteStatus = 'muted';
            }
        }
    },
    mounted(){
        this.$http.post('api_mobile/cat/new_list')
          .then(
            res => {
                if (res.data.status == 'n') {
                    alert(res.data.info)
                }else if(res.data.status == 'y'){
                    this.list = res.data.result
                }

            }
        )
        this.$http.post('api_mobile/goods/index/ad')
          .then(
            res => {
                if (res.data.status == 'n') {
                    alert(res.data.info)
                }else if(res.data.status == 'y'){
                    this.swipedata = res.data.result
                }

            }
        )
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.new{
    background-color: #f2f2f2;
    margin-bottom:50px;
    height:100%;
}
.new .swipe {
    width: 100%;
    height:150px;
}

/* .container ul{
    height: 260px;
} */
.new .container{
    background-color: #ffffff;
    margin: 5px;
    border-radius: 10px;
}
.new .container h3{
    font-size: 14px;
    font-weight: normal;
    color: #383838;
    border-bottom: 1px dashed #acacac;
    padding: 5px;
}
.new .container ul li a {
    display: table;
    width: 100%;
    background-color: #ffffff;
    -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, .2);
    margin-bottom: 8px;
    color: #656565;
}
.new .container ul li a div{
    display: table-cell;
    vertical-align: middle;
}
.new .container ul li a div:first-child{
    width: 70px;
    height: 55px;
    text-align: center;
    font-size: 28px;
}
.new .container ul li a div span img{
    width:30px;
    height:30px;
}
.new .container ul li a div .ico_arrow{
    display: inline-block;
    width: 10px;
    height: 10px;
    vertical-align: -2px;
    border-top: 2px solid #ddd;
    border-right: 2px solid #ddd;
    -webkit-transform: rotate(45deg);
}

.new img {
    overflow: hidden;
    width: 100%;
    height: 100%;
}
</style>



