<template>
  <div class="big_box">
    <div class="cen-content" :model="jiaData">
      <p>卡号 : {{jiaData.card_no}}</p>
      <p>余额 : {{jiaData.total_count}}</p>
    </div>
    <div class="centent">
      <ul>
        <li :v-model="cardData">
          <div class="content_right">
              <span class="title">{{cardData.name}}</span>
              <span class="amount">充值金额：{{cardData.count}}</span>
              <span class="span-top">赠{{cardData.add_money}}</span>
            <p class="warn-recharge">{{cardData.cue}}</p>
          </div>
        </li>
      </ul>

    </div>
    <p class="pay-title">选择支付方式</p>
    <div class="wechatpay">
      <div class="select-pay">
        <img src="../../assets/my/pic/微信支付图标.png"></img>
        <span>微信支付</span>
        <img class="choise" src="../../assets/my/pic/选中.png"></img>
      </div>

    </div>
    <a :href="whatpay">
      <button class="r" size="large"> 立即支付</button>
    </a>
  </div>
</template>

<script>
export default {
  data() {
    return {
      jiaData: {},
      cardData: {},
      order_no: "",
      whatpay: ""
    };
  },
  methods: {},
  created() {
    this.$http
      .get("api_mobile/balance/recharge_detail", { params: this.$route.query })
      .then(res => {
        console.log(res)
        this.jiaData = res.data.result;
        this.cardData = res.data.result.card;
        // let date = new Date(this.cardData.time)
        // let year = date.getFullYear();//年
        // let month = date.getMonth()+1;
        // let day = date.getDate(); //日期
        // this.jiaData.endtime = year + '-'+ month + "-"+ day
      });
    let getParams = window.localStorage.getItem("params");
    let jsonObj = JSON.parse(getParams);
    let pdData = {
      id: this.$route.query.card_id,
      m_id: jsonObj.m_id,
      token: jsonObj.token,
      sign: 68,
      timestamp: 90
    };
    let self = this;
    this.$http
      .get("api_mobile/order/order/recharge", { params: pdData })
      .then(res => {
        self.order_no = res.data.result.order_no;
        self.whatpay = `http://dev.jzfw.yaokexing.com/api/pay/do_pay?order_no=${
          self.order_no
          }&m_id=${jsonObj.m_id}&token=${jsonObj.m_id}&sign=7&timestamp=8&id=${this.$route.query.card_id}`;
      });
  },
  mounted() { }
};
</script>

<style scoped>
.big_box {
  height: 100%;
  position: relative;
  background-color: #f2f2f2;
}

.cen-content {
  margin: 10px;
  padding-bottom:5px;
  border-radius: 5px;
  background-color: #ffffff;
}
.cen-content p{
  color:#00c88d;
  font-size:14px;
}

p {
  border-bottom: 1px solid #e6e6e6;
  line-height:36px;
  padding-left:5px;
}

.centent li {
  margin: 10px;
  border-radius: 5px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
}

.content_right {
  width: 80%;
  margin:10px auto;
  border:1px solid #00c88d;
  border-radius:5px;
  position: relative;
  padding:20px;
}
.content_right span{
 display:block;
}
.content_right .title{
  font-size:30px;
  color:#00c88d;
  margin-bottom: 8px;
}
.content_right .amount{
  font-size:20px;
  color:#00c88d;
}
.content_right .span-top {
  position:absolute;
  right:0;
  top:0;
  font-size:12px;
  padding:3px 20px;
  background-color:#00c88d;
  border-radius: 0 5px 0 20px;
  color:#fff;
}

.wechatpay {
  margin: 10px;
  border-radius: 5px;
  background-color: #ffffff;
  padding: 2px;
}
.wechatpay img{
 float:left;
  width:20px;
  margin:10px;
}
.wechatpay .choise{
  float:right;
  width:18px;
}
.wechatpay span {
  padding-left: 10px;
  line-height:40px;

}


/* .paymoney{
  height:40px;
} */

.paymoney a {
  height: 40px;
  width: 100%;
}
.pay-title{
  border:none;
  margin-left:10px;
}
.warn-recharge{
  color:#f00;
  font-size:16px;
  line-height:16px;
  margin-top:10px;
  border-bottom:none;
  padding-left:0;
}


.r {
  padding: 0 25px;
  background-color: #00c78c;
  height: 38px;
  border-radius: 5px;
  width: 100%;
  color:#fff;
  margin-top:40px;
}
</style>



