<template>
    <div class="introduce">
        <div class="img">
            <img src="../assets/team/team.jpg"/>
        </div>
        <div class="introduce-title">
                <p class="title">强大的清洁队伍</p>
            <p class="title-en">Powerful cleaning team</p>
            <p></p>
            <div class="active-b">
               <div class="list">
                   <div class="demo" v-for="(item, index) in list" :key='index'@click="listnum(item.id)">
                       <div>
                           <!--<img src="../assets/team/people1.jpg"/>-->
                           <img :src= '"http://dev.jzfw.yaokexing.com"+item.photo'/>
                           <p>保洁员：{{item.name}}</p>
                           <p>任务：{{item.job}}</p>
                       </div>

                   </div>
                   <!--
                   <div class="demo">
                       <div>
                           <img src="../assets/team/people2.jpg"/>
                           <p>保洁员：陈女士</p>
                           <p>任务：打扫清洁</p>
                       </div>
                   </div>
                   -->
                   <div class="clear"></div>
               </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'HelloWorld',
    data() {
        return {
            index:2,
            choiseBlock:'displayNo',
            list: []
        }
    },
    mounted(){
        this.$http.post('api_mobile/cat/team_list')
            .then(
                res => {
                    let _this = this
                    console.log(res.data)
                    _this.list = res.data.result
                }
            )
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .introduce{
        padding-bottom:60px;
    }
    .introduce img{
        width:100%;
    }
    .introduce-title .title{
       color: #00C88D;
       font-size:15px;
        text-align:center;
        margin-top:15px;
    }
    .introduce-title .title-en{
        color: #000;
        font-size:8px;
        text-align:center;
        margin-bottom:18px;
    }
    .introduce-title .active-b{
        margin:5px;
        text-align:center;
    }
    .introduce-title .active-b p{
       line-height:20px;
        font-size:12px;
        text-align:left;
    }
    .introduce-title .active-b .list .demo{
        float:left;
        width:50%;
    }
    .introduce-title .active-b .list .demo div{
       margin:20px;
    }
    .introduce-title .active-b .list .demo div img{
        /*border-radius: 50%;*/
    }
    .introduce-title .active-b .list .demo div p{
        text-align:center;
    }
    .clear{clear:both;}



</style>



